.header {
    color: $gray--darken;
    background-color: #fff;

    @include respond-to(xs) {

        .container {
            padding: 0;
        }
    }

    &.header--fixed {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        z-index: 201;
        transition: left 0.3s cubic-bezier(0.25, 0.8, 0.4, 0.95);
    }

    &.bg--dark {
        border-bottom: none;
        background-color: #2f2f2f;

        .header {

            &__top {
                background-color: $color-bg--dark;
            }

            &__bottom {
                background-color: #2f2f2f;
            }

            &__inner {
                border-bottom: none;
            }

            &__title {
                color: $gray--light;
            }
        } //.header.bg--dark .header

        .city-select {

            &__title {
                color: $gray--light;
                border-color: $gray--light;

                &:hover {
                    color: $gray--lighten;
                    border-color: $gray--lighten;
                }
            }
        } //.header.bg--dark .change-city

        .header-user {
            color: $gray--light;

            &::after {
                color: $gray--light;
            }

            &:hover {
                background-color: $color-bg--darken;
            }

            &.is-active {
                background-color: $color-bg--darken;
            }
        } //.header.bg--dark .header-user

        .header-action {

            &__login {
                color: $gray--light;
            }
        } //.header.bg--dark .header-action

        .toggle-mnu {

            &.on {

                span {
                    background-color: transparent;
                }
            }

            span {
                background-color: $gray--light;

                &::before,
                &::after {
                    background-color: $gray--light;
                }
            }
        } //.header.bg--dark .toggle-nme

        .request-info {

            &__icon {

                &::after {
                    border-color: $color-bg--dark;
                }
            }

            &:hover,
            &.is-active {
                background-color: $color-bg--darken;

                .request-info__icon {
                    color: $gray--light;

                    &::after {
                        border-color: $color-bg--darken;
                    }
                }
            }
        } //.header.bg--dark .request-info

        .logo {

            &__link {
                color: #fff;
            }

            span {
                font-family: encode-700, sans-serif;
                color: $color-main;
            }
        } //.header.bg--dark .logo

        .nav {

            &__item {

                &.is-active,
                &:hover {

                    .nav__link {
                        color: $gray--lightest;
                        background-color: #232323;
                    }
                }
            }

            &__link {
                color: $gray--lightest;
            }
        } //.header .bg--dark .nav
    } //.header.bg--dark

    &__top {
        height: $base-header-size;
        position: relative;

        @include respond-to(xs) {
            height: $base-header-size--small;
        }
    } //.header__top

    &__bottom {
        height: 40px;
        @include respond-to(sm) {
            display: none;
        }
    } //.header__bottom

    &__inner {
        height: $base-header-size;
        border-bottom: 1px solid $color-border--lightest;
        @include flexCenter;
        @include respond-to(xs) {
            height: $base-header-size--small;
        }
    } //.header__inner

    &__search {
        max-width: 320px;
        width: 100%;
        @include centre;
        z-index: z(fixed);

        @include respond-to(xs) {
            display: none;
        }
    } //.header__search

    &__city-select {
        margin-left: $gutter * 2;

        @include respond-to(sm) {
            margin-left: $gutter;
        }

        @include respond-to(xs) {
            display: none;
        }
    } //.header__city-select

    &__title {
        margin-left: 30px;
        font-family: $f-bold;

        @include respond-to(md) {
            margin-left: 10px;
        }

        @include respond-to(xs) {
            display: none;
            max-width: 300px;
            font-family: $f-light;
            @include text-overflow;
        }
    } //.header__title

    &__logo {
        margin-top: 4px;
    } //.header__logo

    &--transparent {
        background-color: transparent;
    } //.header--transparent

    .search {
        height: 35px;
        margin: 0 auto;
        flex: 1;
        display: flex;

        @include respond-to(sm) {
            display: none;
        }

        &__group {

            &--city {
                border-right: none;
                border-radius: $border-radius 0 0 $border-radius;
            }
        }

        &__input,
        &__btn {
            height: auto;
            line-height: 35px;
        }

        &__input {
            padding-left: 18px;
        } //.header .search__input

        &__btn {
            height: 35px;
            font-size: 15px;

            span {
                display: block;
                margin-top: 0px;
                line-height: 1;
            }
        }
    } //.header .search
} //.header

.logo {
    display: inline-block;
    font-family: encode-700, sans-serif;
    font-size: 20px;
    letter-spacing: 1px;

    @include respond-to(sm) {
        margin-right: 15px;
    }

    @include respond-to(xs) {
        margin-right: 0;
        flex: 1;
    }

    @include respond-to(i5) {
        font-size: 17px;
    }

    &__link {
        color: #000;

        &:hover {
            color: #000;
        }
    } //.logo__link

    &__img {
        width: 130px;
    } //.logo__img

    span {
        color: $color-main;
    } //.logo span
} //.logo

.header-action {
    margin-left: auto;
    @include flexCenter;
    @include respond-to(sm) {
        margin-left: auto;
    }

    &__inner {
        @include flexCenter;
    } //.header-action__inner

    &__request-info {

        .request-info {
            width: 60px;
            height: $base-header-size;

            @include respond-to(xs) {
                width: 50px;
                height: $base-header-size--small;
            }
        }
    } //.header-action__request-info

    &__fav {
        width: 60px;
        height: $base-header-size;
        color: $color-text;

        @include respond-to(xs) {
            width: 50px;
            height: $base-header-size--small;
        }

        &:hover {
            color: $color-main;
        }
        @include respond-to(i5) {
            width: 35px;
        }
    } //.header-action__fav

    &__search {
        cursor: pointer;
        @include respond-min(sm) {
            display: none;
        }

        @include respond-to(xs) {
            width: 50px;
            height: 50px;
        }

        .icon {
            width: 21px;
            height: 21px;
        }
    } //.header-action__search

    &__login {
        color: $gray--dark;

        &:hover {
            color: $color-main;

            .icon {
                fill: $color-main;
            }
        }

        .icon {
            width: 28px;
            height: 28px;
            fill: $color-main;
            @include tr;
        }
    } //.header-action__login

    &__out {
        @include respond-to(xs) {
            display: none;
        }
    } //.header-action__out

    .button-icon {

        &--fav {

            &.is-checked {

                &::before {
                    color: $color-main;
                }
            }
        }
    }
} //.header-action

.header-user {
    height: $base-header-size;
    padding-right: 30px;
    padding-left: 12px;
    color: $gray--dark;
    cursor: pointer;
    position: relative;
    @include flexCenter;

    @include respond-to(sm) {
        padding: 0 10px;
    }

    @include respond-to(xs) {
        display: none;
        margin-left: 0;
        padding-left: 0;
    }

    &::after {
        content: '\f107';
        @include fa('light');
        color: $gray--dark;
        font-size: 15px;
        @include vy;
        right: 10px;
        @include tr;

        @include respond-to(sm) {
            display: none;
        }
    }

    &:hover {
        background-color: #f1f1f1;
    }

    &.is-active,
    &:hover {
        background-color: #f1f1f1;

        .header-user__dropdown {
            top: 60px;
        }
    }

    &__name {
        font-family: $f-medium;
        font-size: 14px;
        @include respond-to(sm) {
            display: none;
        }
    } //.header-user__name

    &__img {
        width: 35px;
        height: 35px;
        margin-left: 15px;
        border-radius: 50%;
        overflow: hidden;

        @include respond-to(sm) {
            margin-left: 0;
        }
    } //.header-user__img

    &__link {
        @include flexCenter;
    } //.header-user__link

    &__desc {
        font-family: $f-light-i;
        font-size: 13px;
    } //.header-user__desc

    .bb-dropdown {

        &__list {
            padding: 0;
            min-width: 180px;
            left: auto;
            right: 0;
            top: 45px;
            z-index: 200;
            border-right: none;
            border-left: none;
            border-top-color: $color-border--lightest;
            border-bottom: 4px solid $color-main;
            box-shadow: 0 6px 20px rgba(#000, 0.3);

            &::before {
                border-bottom-color: $color-border--lightest;
            }

            ul {
                padding: 0;
                margin: 6px 0 1px;
            } //.header-user ul

            li {
                position: relative;

                &:last-child {

                    &::after {
                        content: '';
                        display: block;
                        width: 84%;
                        height: 1px;
                        background-color: $color-border--light;
                        @include vx;
                        top: -4px;
                    }

                    a {
                        margin-top: 7px;
                    }
                }

                &:hover {
                    background-color: $color-bg--lighten;

                    a {
                        color: #000;
                    }
                }
            } //.header-user li

            a {
                display: block;
                padding: 7px 15px 8px;
                font-size: 13px;
                color: $color-text;
                transition: initial;
            } //.header-user a
        } //.header-user .bb-dropdown__list
    } //.header-user .bb-dropdown

    &[data-dropdown-position^='right'] {

        .bb-dropdown__list {

            &::before,
            &::after {
                right: 42px;
            }
        } //.header-user[data-dropdown-position^='right'] .bb-dropdown__list
    }
} //.header-user

.request-info {
    position: relative;
    cursor: pointer;

    &:hover {
        background-color: #f1f1f1;

        .request-info__icon {
            color: $gray;

            &::after {
                border-color: #f1f1f1;
            }
        }
    }

    &__inner {
        padding: 10px;
        @include flex;
    } //.request-info__inner

    &__header {
        display: block;
        padding: 0 10px;
        height: 40px;
        font-size: 14px;
        color: $gray--darkest;
        line-height: 40px;
        border-bottom: 1px solid $color-border--lightest;

        span {
            font-size: 14px;
            color: $gray--light;
        }
    } //.request-info__header

    &__desc {
        @include flexColumn;
        flex: 1;
    } //.request-info__desc

    &__icon {
        margin: auto;
        color: $gray--light;
        font-size: 20px;
        @include tr;
        @include vcenter;

        &::after {
            content: attr(data-count);
            @extend %badge;
            color: #000;
            background-color: $color-main;
            border: 2px solid #fff;
            position: absolute;
            top: -10px;
            left: 7px;
            animation: pulse 1.5s infinite;
            @include respond-to(xs) {
                animation: none;
            }
            @include respond-to(i5) {
                left: 7px;
            }
        }
    } //.request-info__icon

    &__items {
        max-height: 355px;
        overflow-y: auto;
        @include bb-scroll;
        @include smooth-scroll;
        @include flexColumn;
        @include respond-to(xs) {
            height: calc(100vh - 127px);
            max-height: 100%;
        }
    } //.request-info__items

    &__item {
        border-bottom: 1px solid $color-border--lightest;

        &:hover {
            background-color: $color-bg--lighten;
        }

        &:last-child {
            border-bottom: none;
        }
    } //.request-info__item

    &__avatar {
        display: inline-block;
        margin-right: 10px;
        width: 50px;
        height: $base-header-size--small;
        border-radius: 50%;
        overflow: hidden;
        position: relative;
    } //.request-info__avatar

    &__text {
        margin-bottom: 10px;
        font-size: 13px;
        line-height: 1.3;
        color: $color-text;

        strong {
            font-family: $f-medium;
            font-weight: 500;
            color: #000;
        }
    } //.request-info__text

    &__time {
        display: block;
        line-height: 1.8;
        color: $gray--light;
    } //.request-info__time

    &__buttons {
        display: flex;
        justify-content: flex-start;

        .button-animate {

            &:last-child {
                margin-left: 15px;
            }
        }
    } //.request-info__buttons

    &__footer {
        padding: 5px 0 8px;
        text-align: center;
        background-color: $color-bg--lighten;

        .button-link {
            font-size: 13px;
            line-height: 1;
        }
    } //.request-info__footer

    .button-animate {
        padding: 2px 10px;
        letter-spacing: 0;
        font-size: 12px;
    }

    &[data-dropdown-position^='right'] {

        &.is-active {
            background-color: #f1f1f1;

            &::after {
                border-color: #f1f1f1;
            }

            .request-info__icon {
                color: $gray;

                &::after {
                    border-color: #f1f1f1;
                }
            }

            .bb-dropdown {

                &__list {
                    top: 60px;
                    z-index: 250;

                    @include respond-to(xs) {
                        top: 50px;
                        right: -1px;
                    }
                }
            }
        } //.request-info.is-active

        .bb-dropdown {

            &__list {
                top: 70px;

                @include respond-to(xs) {
                    top: 60px;
                    right: -1px;
                }

                &::before,
                &::after {
                    right: 23px;

                    @include respond-to(xs) {
                        right: 19px;
                    }
                }
            }
        }
    }

    .bb-dropdown {

        &__list {
            min-width: 320px;
            max-width: 377px;
            padding: 0;
            left: auto;
            right: 1px;
            top: 70px;
            @include dropdownResponseWidth;

            @include respond-to(xs) {
                box-shadow: none;
                border-radius: 0;
                top: 60px;
                right: -11px;
            }

            &::before,
            &::after {
                right: 23px;

                @include respond-to(xs) {
                    right: 29px;
                }
            }
        }
    }
} //.request-info
