html {
    overflow-x: hidden;

    &.is-fixed {
        overflow: hidden;

        body {
            overflow: hidden;
        }
    }
}

body {
    height: 100%;
    position: relative;
    min-width: 320px;
    font-family: $font-family-base;
    font-size: $font-size-base;
    color: $color-text;
    line-height: $line-height-base;
    @include smooth-font;
    background-color: #fff;
    scroll-behavior: smooth;
    overflow-x: hidden;
    @include scroll;

    &.is-fixed,
    &.fancybox-enabled {
        overflow: hidden;
    } //body.is-fixed || .fancybox-enabled

    &.loading {

        .preloader {
            background-color: #fff;
            transform: scale(1);
            opacity: 1;
            visibility: visible;
        } //body.loading .preloader

        .btn-floating {
            // display: none;
            bottom: -100px !important;
        } //body.loading .btn-floating
    } //body.loading

    &.loading-animation {

        .preloader {
            transform: translateY(-200vh);
        }

        .btn-floating {
            bottom: -100px !important;
        } //body.loading .btn-floating
    } //body.loading-animation

    &.modal-open {
        padding-right: 0 !important;
    } //body.modal-open
} //body

header, footer, main, div, section, ul, ol {
    @include clr;
}

img {
    @extend %img;
}

select {
    width: 100%;
    outline: none;
    background-color: transparent;
}

textarea {
    width: 100%;
    padding: 10px 15px;
    background-color: transparent;
    resize: none;
    @include bb-scroll--thin;
}

.windows {
    display: none;
}

.clearfix {
    @include clr();
}

.overlay {
    display: none;
    background: rgba(47, 47, 47, 0.35);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: z(overlay);

    &:hover {

        .overlay__btn {
            opacity: 1;
            visibility: visible;
        }
    }

    &.is-visible {
        display: block;
    } //.overlay.is-visible

    &__btn {
        opacity: 0;
        visibility: hidden;
        @include tr;
        @include centre('x');
        top: 100px;
    } //.overlay__btn

    &__icon {
        width: 55px;
        height: 55px;
        margin: auto auto 15px;
        padding: 10px;
        border-radius: 50%;
        background-color: $color-bg--light;
        cursor: pointer;
        @include tr;
        box-shadow: 0 3px 20px rgba(0, 0, 0, 0.25);
        @include flexColumn;
        align-items: center;
        justify-content: center;

        &:hover {
            background-color: $color-bg--lightest;

            .icon {
                @include rotate(90);
            }
        }

        span {
            margin-top: 5px;
            font-size: 14px;
            line-height: 1;
            font-family: $f-bold;
            color: $gray;
            text-transform: uppercase;
        }

        .icon {
            @include tr;
        }
    } //.overlay__icon

    &__text {
        font-family: $f-bold;
        font-size: 14px;
        letter-spacing: 0.5px;
        color: #fff;
        text-transform: uppercase;
    } //.overlay__text

    &--absolute {
        position: absolute;
    } //.overlay--absolute
}

.ico {
    display: inline-block;
}

.icon {
    width: 18px;
    height: 18px;
    fill: $color-icon;

    &-controls {
        width: 23px;
        height: 25px;
        fill: $gray--light;
    }
}

.container {
    @include container;

    //      

    &-flex {
        display: flex;
        flex-wrap: wrap;
    }
}

.loader {
    animation: animate 1.5s linear infinite;
    clip: rect(0, 80px, 80px, 40px);
    height: 80px;
    width: 80px;
    position: fixed;
    top: 50vh;
    left: 50vw;
    margin-left: 100px;

    &::after {
        animation: animate2 1.5s ease-in-out infinite;
        clip: rect(0, 80px, 80px, 40px);
        content: '';
        border-radius: 50%;
        height: 80px;
        width: 80px;
        position: absolute;
    }

    &__wrap {
        display: none;
        background-color: rgba(#fff, 0.7);
        @include coverdiv;
    }

    &--scroll {
        width: 32px;
        height: 32px;
        background-image: url('../img/general/loader.gif');
        background-color: transparent;
        background-repeat: no-repeat;
        @include vx;
        bottom: -42px;
    } //.loader--scroll
} //.loader

.preloader {
    visibility: hidden;
    opacity: 0;
    transform: scale(0);
    background-color: transparent;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 10000;
    transition: visibility 0.5s, opacity 0.5s, transform 0.5s;

    &__icon {
        width: 55px;
        height: 55px;
        position: fixed;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: z(fixed);
    }
} //.preloader

//HELPER CLASSES

.is-hidden {
    display: none !important;
}

.hidden {
    display: none !important;
}

.is-notvisible {
    opacity: 0;
    position: absolute;
    z-index: -1;
}

@each $screen in lg, md, sm, xs {

    .#{$screen}-show {
        display: none !important;

        @include respond-to(#{$screen}) {
            display: block !important;
        }
    }

    .#{$screen}-only {
        display: none !important;

        &--flex {
            display: none !important;
        }
    }
    @include respond-to(#{$screen}) {

        .#{$screen}-only {
            display: block !important;

            &--flex {
                display: flex !important;
            }
        }

        .#{$screen}-hide {
            display: none !important;
        }
    }
}

@for $i from 1 through 30 {

    .mb-#{$i} {
        margin-bottom: $i + px;
    }

    .mt-#{$i} {
        margin-top: $i + px;
    }

    .ml-#{$i} {
        margin-left: $i + px;
    }

    .mr-#{$i} {
        margin-right: $i + px;
    }

    .pb-#{$i} {
        padding-bottom: $i + px;
    }

    .pt-#{$i} {
        padding-top: $i + px;
    }

    .pos-#{$i} {
        order: $i;
    }

    @include respond-to(md) {

        .pos-md-#{$i} {
            order: $i;
        }
    }

    @include respond-to(sm) {

        .pos-sm-#{$i} {
            order: $i;
        }
    }

    @include respond-to(xs) {

        .pos-xs-#{$i} {
            order: $i;
        }
    }
}

.js-mobile-block--show {
    cursor: pointer;
}
