.bb-window {
    min-width: 320px;
    margin-right: -3px;
    padding: 0 !important;
    border-radius: $border-radius;
    box-shadow: 0 1px 60px rgba(0, 0, 0, 0.5);
    overflow: hidden;
    @include respond-to(i5) {
        min-width: 300px;
    }

    &--mobile {
        max-width: 425px;
        width: 100%;
        padding: 0;
        @include max-screen(425) {
            max-width: 414px;
        }
        @include max-screen(375) {
            max-width: 355px;
        }
        @include respond-to(i5) {
            max-width: 320px;
        }
    } //bb-window_mobile

    &-info {
        max-width: 320px;
        text-align: center;
        overflow: initial;

        &__icon {
            width: 53px;
            height: 53px;
            fill: $color-success;
            background-color: #fff;
            border: 3px solid #fff;
            border-radius: 50%;
            @include vx;
            top: -23px;
        }

        &__btn {
            width: 100%;
        }

        .bb-window {

            &__title {
                display: block;
                margin-top: 30px;
                padding: 0 20px;
                text-align: center;
                font-size: 17px;
            }

            &__body {
                padding-top: 5px;
            }

            &__desc {
                font-size: 15px;
            }
        }

        .fancybox-close-small {
            display: none;
        }
    } //bb-window--info

    &__wrap {

        .fancybox-close-small {

            &:hover {

                &::after {
                    color: #000;
                    background-color: transparent;
                }
            }

            &::after {
                font-size: 24px;
                color: $gray--lighten;
                background-color: transparent;
                top: 7px;
                right: 4px;
            }
        }

        .fancybox-toolbar {
            display: none;
        }
    } //bb-window__wrap

    &__header {
        @extend %cardHeader;
        background-color: #fff;
    } //bb-window__header

    &__body {
        padding: 20px;
    } //bb-window__body

    &__footer {
        padding: 0 20px 20px;
        text-align: center;
    } //bb-window__footer

    &__title {
        margin-top: 0;
        line-height: 45px;
        font-size: 16px;
        font-family: $f;
        font-weight: 400;
        color: $gray--darken;
    } //bb-window__title

    &__warning {
        font-size: 14px;
        color: $gray--darken;

        p {
            display: block;
            margin-bottom: 20px;
        }
    } //bb-window__warning

    &__textarea {
        width: 100%;
        height: 150px;
        padding: 10px 15px;
        border: 1px solid $color-border;
        border-radius: $border-radius;
        resize: none;
    } //bb-window__textarea

    &__user-contacts {
        display: inline-block;
        vertical-align: middle;
        width: 47%;
        margin-bottom: 15px;
        padding: 5px 15px;
        border: 1px solid $color-border;
        border-radius: $border-radius;

        &:first-child {
            margin-right: 35px;
        }
        @include respond-to(xs) {
            width: 100%;

            &:first-child {
                margin-right: 0;
            }
        }
    } //bb-window__user-contacts

    &__buttons {
        padding: 10px 20px;
        border-top: 1px solid $color-border--lightest;
        background-color: $color-bg--lighten;
        @include flexCenter;
        justify-content: flex-end;
        @include respond-to(xs) {
            justify-content: center;
        }

        .button-animate {
            padding: 5px 30px;
            font-size: 14px;
        }

        .button-link {
            margin-right: 30px;
            font-size: 15px;

            &:hover {
                text-decoration: none;
            }
        }
    } //bb-window__button

    .tab {

        &__titles {
            margin-bottom: 25px;
            overflow: hidden;
            justify-content: center;
        }

        &__content {
            padding: 0;

            .form {
                padding: 0;
                width: auto;
                max-width: 100%;
            }
        }
    }
} //bb-window

#auth-form {

    .fancybox-close-small {
        top: 4px;
        right: 4px;
    }

    .form__title {
        display: inline-block;
        margin-top: 5px;
        font-size: 17px;
        color: $gray--light;
        cursor: pointer;

        &:hover {
            color: $color-text;
        }

        &.is-active {
            color: $color-text;
            border-bottom: 2px solid $color-main;
        }

        & + .form__title {
            margin-left: 15px;
        }
    }
} //#auth-form

.fast-entry {
    padding: 30px;
    max-width: 375px;
    width: 375px;
    overflow: hidden;
    @include respond-to(i5) {
        max-width: 300px;
        width: 300px;
    }

    .bb-input {
        width: 100%;
        margin-bottom: 20px;

        &__input {
            width: 100%;
        }
    }
} //.fast-entry

.review-add {
    position: relative;

    &.is-success {

        .review-add__success {
            @include tY(0);
        }
    }

    &__title {
        display: block;
        margin-bottom: 10px;
        font-family: $f-medium;
        font-size: 15px;
    } //review-add__title

    &__text {
        min-height: 100px;
        width: 100%;
        padding: 10px 15px;
        font-family: $f-light;
        font-size: 15px;
        color: $gray--darkest;
        resize: none;
        border-color: $color-border--lighten;
        border-radius: $border-radius;
        @include bb-scroll;
    } //review-add__text

    &__success {
        padding: 20px;
        z-index: z(success);
    } //review-add__success

    .star-rating {
        margin-bottom: 20px;
    }
} //review-add
