.page-catalog,
.page-contacts,
.page-lk,
.page-error,
.page-rules {

    .footer {
        background-color: $color-bg--lighten;
    }
}

.footer {
    border-top: 1px solid $color-border--lightest;

    &__inner {
        @include flexCenter;
        @include respond-to(xs) {
            flex-wrap: wrap;
            flex-direction: column;
        }
    } //footer__inner

    &__top {
        padding: 15px 0;
    } //footer__top

    &__nav {
        flex: 1;
        @include respond-to(xs) {
            margin-bottom: 15px;
        }
    } //footer__nav

    &__bottom {
        padding: 15px 0;
        border-top: 1px solid $color-border--lightest;
    } //footer__bottom
} //footer

.footer-nav {

    &__list {
        @include flexCenter;
        @include respond-to(xs) {
            text-align: center;
            justify-content: center;
        }
    } //footer-nav__list

    &__item {

        & + .footer-nav__item {
            margin-left: 30px;
            @include respond-to(xs) {
                margin-left: 15px;
            }
        }
    } //footer-nav__item

    &__link {
        font-size: 14px;
        color: $gray;
    } //footer-nav__link
} //footer-nav

.copyright {
    font-family: $f-light;
    font-size: 13px;
    color: $gray--light;

    &__desc {
        @include respond-to(sm) {
            display: none;
        }
    } //copyright__desc
} //copyright

.agreement {
    margin-left: auto;
    font-family: $f-light;
    font-size: 13px;
    color: $gray--light;
    @include flex;

    @include respond-to(xs) {
        flex-wrap: wrap;
        justify-content: center;

        a {

            &:first-child {
                margin-right: 0;
            }

            &:last-child {
                margin-left: 0;
            }
        }

        span {
            display: none;
        }
    }

    &__link {
        color: $gray--light;

        &:hover {
            text-decoration: underline;
            color: $gray;
        }

        &:first-child {
            margin-right: 10px;
        }

        &:last-child {
            margin-left: 10px;
        }
    } //agreement__link
} //agreement
