#nprogress {

    .bar {
        background: $color-main;
    }

    .peg {
        box-shadow: 0 0 10px $color-main, 0 0 5px $color-main;
    }

    .spinner-icon {
        border-top-color: $color-main;
        border-left-color: $color-main;
    }

    .spinner {
        display: none;
    }
} // #nprogress
