.bb-slider {
    position: relative;

    &__arrow {
        display: inline-block;
        @include vy;
        cursor: pointer;
        outline: none;
        z-index: z(default);

        &.slick-disabled {
            // display: none !important;
            opacity: 0.4;
        }

        &::before {
            @include fa('light');
            font-size: 42px;
            color: $gray--darkest;
            opacity: 0.3;
        }

        &:hover {

            &::before {
                opacity: 1;
            }
        } //.bb-slider__arrow

        &--prev {
            left: -20px;

            &::before {
                content: '\f104';
            }
            @include respond-to(xs) {
                left: 43%;
            }
        } //.bb-slider__arrow--prev

        &--next {
            right: -20px;

            &::before {
                content: '\f105';
            }
            @include respond-to(xs) {
                right: 43%;
            }
        } //.bb-slider__arrow--next
    } //.bb-slider__arrow

    &__dots {
        padding: 2px 3px 1px 6px;
        font-family: $f-num;
        font-size: 10px;
        letter-spacing: 3px;
        color: #fff;
        border-radius: 500px;
        position: absolute;
        top: 10px;
        right: 10px;
        background-color: rgba(0, 0, 0, 0.55);
    } //.bb-slider__dots

    &--second {

        .bb-slider {

            &__arrow {
                display: block;
                width: 50px;
                height: 50px;
                background-color: #fff;
                box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
                border-radius: 50%;
                bottom: auto;
                top: 50%;
                transform: translateY(-50%);
                @include max-screen(1024) {
                    width: 40px;
                    height: 40px;
                    border-radius: 0;
                }

                &::before {
                    line-height: 28px;
                    font-size: 22px;
                    color: $color-main;
                    opacity: 1;
                    @include vy;
                }

                &:hover {
                    background-color: $color-main;

                    &::before {
                        color: #fff;
                    }
                }

                &--prev {
                    left: -25px;
                    @include max-screen(1024) {
                        left: 5px;
                    }

                    &::before {
                        content: '\f104';
                        left: 40%;
                    }
                } //.bb-slider__arrow--prev

                &--next {
                    right: -25px;
                    @include max-screen(1024) {
                        right: 5px;
                    }

                    &::before {
                        content: '\f105';
                        left: 45%;
                    }
                }
            }

            &__dots {
                @include respond-min(xs) {
                    display: none;
                }
            }
        }
    } //.bb-slider--second

    &--main {

        .bb-slider {

            &__slides {
                position: relative;
                overflow: hidden;

                @include respond-to(xs) {
                    overflow: initial;
                }
            }
        }

        .slick-slider {

            &:hover {

                .slick-prev {
                    left: 0;
                }

                .slick-next {
                    right: 0;
                }
            }
        }

        .slick-slide {
            margin: 0;
        }

        .slick-arrow {
            width: 44px;
            height: 100%;
            position: absolute;
            top: 0;
            bottom: 0;
            transition: $trsMenu;

            &.slick-disabled {
                // display: none !important;
                opacity: 0.4;
            }

            &::before {
                display: none;
            }
        }

        .slick-prev {
            left: -44px;
        }

        .slick-next {
            right: -44px;
        }
    } //.bb-slider--main

    &--promo {

        .bb-slider {

            &__arrow {
                @include respond-to(xs) {
                    display: none;
                }
            }
        }
    } //.bb-slider--promo

    &--adv {

        .bb-slider {

            &__item {
                margin-bottom: 20px;
            }
        }
    } //.bb-slider--adv

    &--zoom {

        .slick-slide {
            @include scale(0.7);
            @include tr(0.5s);
            transition-delay: 0.2s;

            &.slick-current {
                @include scale(1);
            }
        }
    } //.bb-slider--zoom

    &--triumph {
        background-color: #fbfbfb;

        .slick-slide {
            margin: 0;
        }

        .slick-dots {
            bottom: 13px;
        }
    } //.bb-slider--triumph

    .slick-arrow {
        display: flex;

        &::after {
            content: '';
            display: block;
            margin: auto;
            z-index: z(fixed);
        }

        &.slick-disabled {
            display: none !important;
        }
    } //.bb-slider .slick-arrow

    .slick-prev {

        &::before {
            left: 0;
        }

        &::after {
            @include sprite('angle--left');
        }
    } //.bb-slider .slick-prev

    .slick-next {

        &::before {
            right: 0;
        }

        &::after {
            @include sprite('angle--right');
        }
    } //.bb-slider .slick-next
} //.bb-slider
