//Custom Webkit Scroll
@mixin bb-scroll {
    @include scroll;
    @include scrollbar {
        width: 5px;
    }
    @include scrollbar-track {
        width: 5px;
    }
    @include scrollbar-thumb {
        background-color: $gray--light;
    }
}
@mixin bb-scroll--thin {
    @include scroll;
    @include scrollbar {
        width: 3px;
    }
    @include scrollbar-track {
        width: 3px;
    }
    @include scrollbar-thumb {
        background-color: $gray--lighten;
        border-radius: 100px;
    }
}

@mixin container {
    max-width: 1200px;
    margin-left: auto;
    margin-right: auto;
    @include respond-to(lg) {
        padding-left: 10px;
        padding-right: 10px;
    }

    &--sm {
        max-width: 768px;
    }
}

@mixin flex {
    display: flex;
    justify-content: space-between;
}

@mixin flexCenter {
    display: flex;
    align-items: center;
}

@mixin flexColumn {
    display: flex;
    flex-direction: column;
}

@mixin dropdownResponseWidth {
    @include respond-to(xs) {
        max-width: 427px;
        width: 427px;
    }
    @include respond-to(i8) {
        width: 416px;
    }
    @include respond-to(i7) {
        width: 377px;
    }
    @include respond-to(i5) {
        width: 322px;
    }
}

@mixin responseWidth {
    @include respond-to(xs) {
        max-width: 425px;
        width: calc(425px - 20px);
    }
    @include respond-to(i8) {
        width: calc(414px - 20px);
    }
    @include respond-to(i7) {
        width: calc(375px - 20px);
    }
    @include respond-to(i5) {
        width: calc(320px - 20px);
    }
}

@mixin sectionOverlay {
    position: relative;

    &::before {
        display: block;
        content: '';
        background-color: rgba($color-bg, 0.7);
        @include tr;
        @include coverdiv;
    }

    & > * {
        z-index: z(default);
    }

    .container {
        position: relative;
    }
}

@mixin borderTop {
    border-top: 1px solid $color-border--lightest;

    &:first-child {
        border-top: none;
    }
}

@mixin listIcon($positionTop: 12px, $color: $color-main) {
    padding-left: 15px;
    position: relative;

    &::before {
        content: '';
        display: block;
        width: 8px;
        height: 2px;
        background-color: $color;
        position: absolute;
        top: $positionTop;
        left: 0;
    }
} //ul li &::before (-)

@mixin itemGutter($g: $gutter) {
    margin-bottom: $g;

    &:last-child {
        margin-bottom: 0;
    }
}

@mixin fildsReset {
    font-size: 16px;
    height: 37px;
    line-height: 1;
    padding-left: 0;
    border-left: 0;
    border-right: 0;
    border-top: 0;
    border-radius: 0;
    box-shadow: none;

    &:focus {
        border-bottom-color: $color-info;
    }
}

@mixin border($direction) {
    border-#{$direction}: 1px solid $color-border--lighten;
}

@mixin visibleBlock {
    display: none;
    background-color: #fff;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: z(dropdown) - 1;

    @include respond-to(md) {
        display: block;
        opacity: 0;
        transform: translate(0, 200%);
        will-change: transform, opacity;
        transition: $trs;
    }

    &.is-open {
        display: block;
        z-index: z(dropdown);

        @include respond-to(md) {
            opacity: 1;
            transform: translate(0);
        }
    }
}

@mixin textShadow($shadow: 2px 2px 4px rgba(0, 0, 0, 0.7)) {
    text-shadow: $shadow;
}

%img {
    display: block;
    width: 100%;
    margin: 0;
}

%box {
    border-radius: $border-radius;
    background-color: #fff;
    box-shadow: $bxs-box;
}

%boxWidget {
    border-radius: 10px;
    background-color: #fff;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.1);
}

%badge {
    min-width: 20px;
    height: 19px;
    padding: 3px 5px;
    text-align: center;
    font-family: $f-num--b;
    font-size: 9px;
    color: #000;
    border-radius: 500px;
    overflow: hidden;
    @include flexCenter;
    justify-content: center;
}

%textLight {
    font-family: $f-light;
    font-weight: 300;
}

%titleSmall {
    font-size: 15px;
    color: $gray--darkest;
    border-bottom: 1px solid $color-border--lightest;
    background-color: $color-bg--lighten;
}

%titleSmallBold {
    display: block;
    margin-bottom: 10px;
    font-family: $f-bold;
    font-size: 15px;
    color: $gray--darkest;
}

%cardHeader {
    padding: 10px 10px 10px 15px;
    font-family: $f-medium;
    font-weight: 500;
    font-size: 14px;
    color: $gray--darkest;
    background-color: $color-bg--light;
    border-bottom: 1px solid $color-border--lightest;
    border-radius: $border-radius $border-radius 0 0;
    display: flex;
    align-items: center;

    @include respond-to(xs) {
        padding: 10px;
        background-color: $color-bg--light;
        border-radius: 0;
    }
}

%headerLkStyle {
    height: 50px;
    padding: 0 20px;
    font-family: $f-medium;
    font-size: 16px;
    color: $gray--darkest;
    background: $color-bg--light;
    border-bottom: 1px solid #eee;
    border-radius: 2px 2px 0 0;
    outline: none;
    display: flex;
    align-items: center;

    @include respond-to(xs) {
        padding: 0 10px;
    }
}

%styleInput {
    height: 40px;
    line-height: 40;
    padding: 9px 25px 9px 10px;
    font-size: 16px;
    line-height: 1;
    color: $gray--darkest;
    border-radius: $border-radius;
    border: 1px solid $color-border--light;
    transition: border 0.3s ease;

    &:hover {
        border-color: $color-border;
    }

    &:focus {
        font-size: 16px;
        border-color: $color-border--lighten;
        box-shadow: $bxs-box;
    }

    &:disabled {
        background-color: $gray--lighten !important;
        border-color: $gray--lighten !important;
    }

    @include placeholder {
        font-family: $f-light-i;
        font-size: 14px;
        line-height: 23px;
        color: $gray--light;

        @include respond-to(xs) {
            font-size: 16px;
        }
    }
}

%xs50 {
    @include respond-to(xs) {
        width: $base-header-size--small;
        height: $base-header-size--small;
    }
}

//Reset box style in mobile device
@mixin resetBox {
    margin: 0 -10px 20px;
    box-shadow: none;
    border-top: 1px solid $color-border--lightest;
    border-bottom: 1px solid $color-border--lightest;
    border-radius: 0;
}
