.catalog-item {
    margin-bottom: 15px;
    padding: 20px 20px 0;
    @extend %box;
    position: relative;

    @include respond-to(xs) {
        margin: 0 -10px 15px;
        padding: 0;
        border-radius: 0;
        box-shadow: none;
        border-bottom: 1px solid $color-border--lighten;
        flex-direction: column;
    }

    &:last-child {
        margin-bottom: 0;
    }

    &:hover {
        @include respond-min(xs) {
            box-shadow: 1px 1px 10px rgba($color-main, 0.6);
        }

        .catalog-item {

            &__buttons {
                opacity: 1;
                visibility: visible;
            }
        }

        .properties {

            &__icon {
                fill: $color-text;
            }
        }
    }

    &__wrap {
        position: relative;
        @include flexColumn;
    } //.catalog-item__wrap

    &__inner {
        margin-bottom: 13px;
        @include flex;

        @include respond-to(xs) {
            margin-bottom: 0;
            flex-wrap: wrap;
        }
    } //.catalog-item__inner

    &__img {
        width: 350px;
        max-width: 350px;
        margin-right: $gutter;
        // border-radius: $border-radius $border-radius 0 0;
        overflow: hidden;
        position: relative;

        @include respond-to(xs) {
            max-width: 100%;
            width: auto;
            margin-right: 0;
        }
    } //.catalog-item__img

    &__text {
        display: flex;
        flex-direction: column;
        width: calc(100% - 370px);
        @include max-screen(1024) {
            padding: 10px 15px;
        }
        @include respond-to(sm) {
            width: 100%;
            margin-left: 10px;
            padding: 0;
        }
        @include respond-to(xs) {
            margin-left: 0;
        }
    } //.catalog-item__text

    &__header {
        position: relative;
        @include flexCenter;

        @include respond-to(xs) {
            padding: 5px 10px 10px;
        }

        &-text {
            flex: 1;
        }
    } //.catalog-item__header

    &__avatar {
        width: 40px;
        height: 40px;
        margin-right: 10px;
        border-radius: 50%;
        overflow: hidden;
        @include min-screen(768) {
            display: none;
        }
    } //.catalog-item__avatar

    &__title {
        margin-top: 0;
        margin-bottom: 5px;
        font-size: 18px;
        font-family: $f;
        font-weight: 400;
        color: #000;
        @include respond-to(sm) {
            margin-bottom: 10px;
            order: 2;
        }
        @include respond-to(xs) {
            margin-bottom: 5px;
            font-family: $f-medium;
            font-size: 16px;
        }
    } //.catalog-item__title

    &__address {
        margin-bottom: 15px;
        @include respond-to(sm) {
            font-size: 13px;
            line-height: 1.2;
            order: 3;
        }
        @include respond-to(xs) {
            margin-bottom: 0;
        }

        .place-text {

            &__address {

                &::before {
                    left: 4px;
                    color: #d5d5d5;
                }
            }
        }
    } //.catalog-item__address

    &__info {
        margin-bottom: 10px;
        position: relative;
        @include flexCenter;
        @include respond-to(sm) {
            margin-bottom: 0;
            padding: 5px 0;
            order: 1;
        }
        @include respond-to(xs) {
            padding: 5px 10px;
        }
    } //.catalog-item__info

    &__rating {
        margin-right: 40px;
    } //.catalog-item__rating

    &__properties {
        @include respond-to(sm) {
            margin-left: auto;
        }
    } //.catalog-item__properties

    &__desc {
        display: block;
        max-height: 48px;
        margin-bottom: 20px;
        font-family: $f-light;
        font-size: 13px;
        line-height: 1.3;
        color: #000;
        overflow: hidden;
        position: relative;
        @include respond-to(xs) {
            display: none;
        }

        .fa {
            position: absolute;
            bottom: -3px;
            right: 26px;
            background-color: #fff;
        }

        .button-link {
            position: absolute;
            right: 0;
            bottom: 0;
            background-color: #fff;
        }
    } //.catalog-item__desc

    &__bottom {
        display: flex;
        align-items: center;
        margin-top: auto;
    } //.catalog-item__bottom

    &__buttons {
        display: flex;
        align-items: center;
        margin-left: auto;
        visibility: hidden;
        opacity: 0;
        @include tr;
        @include respond-to(sm) {
            margin-left: 0;
            padding: 10px 0;
            visibility: visible;
            opacity: 1;
            justify-content: center;
            border-top: 1px solid $color-border--lightest;
        }
        @include respond-to(i5) {
            margin-bottom: 10px;
            opacity: 1;
            visibility: visible;

            .icon {
                display: block;
            }
        }

        .button-animate {
            font-size: 14px;

            &:first-child {
                margin-right: 15px;
            }
        }
    } //.catalog-item__buttons

    &__btn {

        &-subscribe {
            margin-left: auto;
            padding: 3px 15px 4px;
            font-family: $f-light;
            font-size: 13px;
        }
    } //.catalog-item__btn

    &__fav {
        display: inline-flex;
        align-items: center;
        width: 35px;
        height: 35px;
        color: $gray--lighten;
        background-color: transparent;
        border: none;
        cursor: pointer;
        position: absolute;
        right: -6px;
        top: -6px;
        z-index: 3;
        @include tr;
        @include respond-to(sm) {

            &::before {
                color: $gray;
            }

            &.is-checked {

                &::before {
                    color: $color-main;
                }
            }
        }
        @include respond-to(xs) {
            right: 5px;
            top: -2px;
        }

        &::before {
            margin: auto;
            font-size: 21px;
            line-height: 21px;
        }
    } //.catalog-item__fav

    &__who {
        color: #000;
        font-family: $f-medium-i;
        font-size: 14px;
        @include flexCenter;

        .num {
            margin-left: auto;
            font-family: $f-num--m;
            color: $gray--light;
            @include respond-to(xs) {
                width: 100%;
                text-align: right;
            }

            small {
                font-family: $f-i;
                font-size: 14px;
            }
        }
    } //.catalog-item__who

    &--small {
        display: block;
        width: 300px;
        padding: 0;
        margin-right: auto;
        border-radius: $border-radius;
        @include flexColumn;

        &:last-child {
            margin-bottom: auto;
        }

        .catalog-item {

            &__wrap {
                flex-direction: row;
            }

            &__inner {
                flex-direction: column;
                margin-bottom: 0;
            }

            &__img {
                max-width: 100%;
                width: 100%;
                margin-right: 0;
            }

            &__header {
                padding: 0 10px;
            }

            &__fav {
                top: 2px;
                right: 5px;
            }

            &__text {
                width: 100%;
                margin-left: 0;

                @include respond-to(md) {
                    padding: 0;
                }

                @include respond-to(xs) {
                    padding-top: 15px;
                }
            }

            &__avatar {
                display: block;
            }

            &__info {
                margin-bottom: 0;
                padding: 5px 10px;
            }

            &__title {
                font-size: 15px;
            }

            &__address {
                margin-bottom: 5px;
                font-size: 13px;
                line-height: 1.2;
            }

            &__desc {
                display: none;
            }

            &__rating {
                margin-right: 0;
            }

            &__properties {
                margin-left: auto;
            }

            &__buttons {
                padding: 10px;
                border-top: 1px solid $color-border--lightest;
                opacity: 1;
                visibility: visible;
                @include flexCenter;
                justify-content: center;
            }
        }

        .price {

            &__item {
                padding: 8px 10px;
            }
        }

        .button-animate {
            padding: 5px 20px;
        }
    } //.catalog-item--small

    &--lk {
        padding: $gutter;
        @include respond-to(xs) {
            padding: $gutter/2;
        }

        .page-title {

            &__wrap {
                @include respond-to(xs) {
                    min-height: initial;
                }
            }
        }

        .catalog-item {
            @include respond-to(xs) {

                &__header,
                &__info {
                    padding: 0;
                }
            }

            &__info {
                @include respond-to(xs) {
                    flex-wrap: wrap;
                }
            }

            &__inner {
                margin-bottom: 0;
            }

            &__title {
                font-family: $f-medium;
            }

            &__text {
                width: 100%;
            }

            &__desc {
                max-height: initial;
                margin-bottom: $gutter/2;
                font-size: 14px;
                overflow: auto;
                @include respond-to(xs) {
                    display: block;
                    margin-top: $gutter/2;
                }
            }

            &__address {
                margin-bottom: 0;
                @include respond-to(md) {
                    order: initial;
                }
            }

            &__who {
                @include respond-to(xs) {
                    flex-wrap: wrap;

                    .num {
                        width: 100%;
                        text-align: right;
                    }
                }
            }
        }

        .price {
            border-top: none;

            &__num {
                font-family: $f-num--m;
                font-size: 15px;

                i {
                    font-size: 12px;
                }
            }
        }

        .properties {
            margin-left: $gutter/2;
            @include respond-to(xs) {
                margin-left: 0;
                width: 100%;

                &__list {
                    flex-wrap: wrap;
                }
            }

            &__item {
                width: auto;
                height: auto;
                color: $gray--darkest;
                font-family: $f-bold-i;
                font-size: 14px;
                position: relative;
                cursor: default;

                &::before {
                    content: '/';
                    margin-right: $gutter/2;
                }
                @include respond-to(xs) {

                    &:first-child {

                        &::before {
                            display: none;
                        }
                    }
                }
            }
        }

        .place-text {
            display: inline-block;
            font-family: $f-medium-i;

            &__metro {
                display: inline-block;
            }
        }
    } //.catalog-item--lk

    .price {

        &__list {
            padding-bottom: 4px;
        }
    }
} //.catalog-item

.properties {

    &__list {
        display: flex;
        align-items: center;
    } //.properties__list

    &__item {
        width: 25px;
        height: 25px;
        line-height: 25px;
        cursor: help;
        @include flexCenter;
        justify-content: center;

        .icon {
            display: block;
            width: 23px;
            height: 23px;
            margin: auto;
            fill: #000;
        }

        i {
            font-size: 20px;
            line-height: 1;
            color: #000;
        }

        & + & {
            margin-left: 5px;
            @include respond-to(xs) {
                margin-left: 5px;
            }
        }

        &_procent,
        &_sale {

            .icon {
                width: 13px;
                height: 13px;
            }
        }

        &_car {

            .icon {
                width: 20px;
                height: 20px;
            }
        }

        .bb-dropdown__list {
            left: -5px;
            font-size: 13px;
            color: #fff;
            border: none;
            background-color: rgba($color-btn--darken, 0.95);

            &::before {
                width: 10px;
                height: 10px;
                border: none;
                border-top-right-radius: 2px;
                background-color: rgba($color-btn--darken, 0.95);
                top: -5px;
            }
        } //.properties__item.bb-dropdown__list
    } //.properties__item

    &__title {
        font-size: 13px;
    } //.properties__title
} //.properties

.price {

    &__list {
        @include flex;
        flex-direction: column;
    } //.price__list

    &__item {
        padding: 8px 0;
        position: relative;
        border-top: 1px solid $color-border--lightest;
        @include flexCenter;

        @include respond-to(xs) {
            padding: 8px 10px;
        }
    } //.price__item

    &__inner {
        width: 50%;
        @include text-overflow;

        &:last-child {
            text-align: right;
        }
    } //.price__inner

    &__num {
        margin-left: auto;
        font-family: $f-num;
        font-size: 16px;
        text-align: right;
        color: #000;

        &_wrap {
            margin-left: auto;

            .price__num {
                margin-left: 15px;
            }
        }

        &_old {
            font-size: 14px;
            color: $color-text--light;
            text-decoration: line-through;

            i {
                font-size: 12px !important;
            }
        }

        i {
            margin-left: 7px;
            font-size: 16px;
        } // .price__num i

        span {
            color: $gray--light;
            font-family: oswald-300, snas-serif;
        }
    } //.price__num

    &__text {
        margin-right: 15px;
        font-size: 14px;
        color: $color-text--darkest;
        white-space: nowrap;
    } //.price__text

    &__time {
        font-family: $f-light;
        font-size: 14px;
        color: $color-text--light;
        @include respond-to(xs) {
            display: none;
        }

        i {
            margin-right: 7px;
            font-size: 13px;
        }
    } //.price__time
} //.price

.place-text {
    font-size: 14px;
    font-family: $f-light;
    color: #000;
    position: relative;

    &--icon {
        padding-left: 30px;
    } //.place-text--icon

    &__wrap {
        @include flexCenter;
        @include respond-to(xs) {
            flex-wrap: wrap;
        }

        &:last-child {

            .place-text__box {
                margin-bottom: 0;
            }
        }

        .place-text__box {

            &:first-child {
                width: 60%;
                @include respond-to(xs) {
                    width: 100%;
                    margin-bottom: 0;
                }
            }

            &:last-child {
                width: 40%;
                @include respond-to(xs) {
                    width: 100%;
                }
            }
        }
    } //.place-text__metro

    &__box {
        margin-bottom: $gutter;

        .place-text {
            margin-bottom: 0;
        }
    }

    &__metro {
        display: block;
        position: relative;
    } //..place-text__metro

    &__address {
        display: block;
    } //.place-text__address
} //.place-text
