.fancybox-slide--html {
    padding: 5px 5px 0;
}

.fancybox-container {
    z-index: z(modal);
}

.fancybox-is-open .fancybox-bg {
    background-color: rgba($gray--darkest, 0.8);
}

.compensate-for-scrollbar {
    margin-right: 0px !important;
}

.fancybox-slide--html .fancybox-content {
    margin-bottom: 0;
}

.fancybox-show-infobar .fancybox-infobar {
    display: none;
}

.fancybox-can-drag .fancybox-content {
    cursor: default;
}

.fancybox-slide {
    @include bb-scroll;
}

.fancybox-close-small {
    width: 25px;
    height: 25px;
    padding: 0;
    top: 9px;
    right: 9px;
    z-index: z(successBtnClose);

    &::after {
        font-size: 23px;
        top: 7px;
        right: 7px;
    }

    &:hover {

        &::after {
            background-color: transparent;
        }
    }

    svg {
        width: 25px;
        height: 25px;
    }
}

.fancybox-toolbar {
    top: auto;
    bottom: 0;
}
