.menu {
    max-width: 280px;
    width: 100%;
    padding-top: 30px;
    background-color: #fff;
    box-shadow: 5px 0 10px rgba(#000, 0.2);
    transition: $trsMenu;
    position: fixed;
    top: 0;
    left: -100%;
    bottom: 0;
    z-index: z(modal);
    will-change: left;
    @include flexColumn;

    @include respond-to(xs) {
        max-width: 320px;
    }

    &.is-open {
        transition: $trsMenu;
        left: 0;
    }

    &__logo {
        margin-bottom: 30px;
        @include flexColumn;
        justify-content: center;
        align-items: center;

        img {
            width: 100px;
            height: 100px;
            margin-bottom: $gutter;
            border-radius: 50%;
        }

        span {
            font-family: $f-medium;
        }
    } //.menu__logo

    &__list {
        @include flexColumn;
        flex-grow: 1;
    } //.menu__list

    &__item {
        position: relative;
        border-bottom: 1px solid $color-border--lightest;

        &:first-child {
            border-top: 1px solid $color-border--lightest;
        }

        &--last {
            margin-top: auto;
            border-top: 1px solid $color-border--lightest;
            border-bottom: 0;

            .menu__link {
                padding: 10px 15px 8px;
            }
        }

        &.is-active {
            background-color: $color-bg--lightest;

            &::before {
                content: '';
                display: block;
                width: 2px;
                height: 100%;
                background-color: $color-main;
                position: absolute;
                left: 0;
                top: 0;
            }

            .menu__link {
                color: #000;
            }
        }
    } //.menu__item

    &__link {
        display: block;
        padding: 10px 15px;
        font-size: 15px;
        color: $gray;

        &:hover {
            color: #000;
            background-color: $color-bg--lightest;
        }
    } //.menu__link

    &__close {
        padding: 10px;
        font-size: 20px;
        cursor: pointer;
        position: absolute;
        top: 0;
        right: 0;

        &:hover {
            color: #000;
        }
    } //.menu__close
} //.menu

.toggle-mnu {
    display: block;
    width: 50px;
    height: 50px;
    padding: 11px;
    cursor: pointer;

    span {
        position: relative;
        display: block;
        width: 100%;
        height: 2px;
        margin-top: 4px;
        background-color: $gray--darkest;
        transition: all 0.3s;
        backface-visibility: hidden;
        border-radius: $border-radius;

        &::after,
        &::before {
            content: '';
            height: 2px;
            width: 100%;
            background-color: $gray--darkest;
            border-radius: $border-radius;
            backface-visibility: hidden;
            transition: all 0.3s;
            position: absolute;
            left: 0;
            top: 9px;
        }

        &::after {
            top: 18px;
        }
    }

    &.on {

        span {
            background-color: transparent;

            &::before {
                transform: rotate(45deg) translate(-1px, 0px);
            }

            &::after {
                transform: rotate(-45deg) translate(6px, -7px);
            }
        }
    }
} //.toggle-mnu
