.overlay, .bb-dropdown__overlay, .menu__overlay {
  display: none;
  background: rgba(47, 47, 47, 0.35);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
}

.overlay:hover .overlay__btn, .bb-dropdown__overlay:hover .overlay__btn, .menu__overlay:hover .overlay__btn {
  opacity: 1;
  visibility: visible;
}

.overlay.is-visible, .is-visible.bb-dropdown__overlay, .is-visible.menu__overlay {
  display: block;
}

.overlay__btn {
  opacity: 0;
  visibility: hidden;
  -webkit-transition: all 0.35s ease;
  -moz-transition: all 0.35s ease;
  -ms-transition: all 0.35s ease;
  -o-transition: all 0.35s ease;
  transition: all 0.35s ease;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 100px;
}

.overlay__icon {
  width: 55px;
  height: 55px;
  margin: auto auto 15px;
  padding: 10px;
  border-radius: 50%;
  background-color: #eee;
  cursor: pointer;
  -webkit-transition: all 0.35s ease;
  -moz-transition: all 0.35s ease;
  -ms-transition: all 0.35s ease;
  -o-transition: all 0.35s ease;
  transition: all 0.35s ease;
  box-shadow: 0 3px 20px rgba(0, 0, 0, 0.25);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.overlay__icon:hover {
  background-color: #f7f7f8;
}

.overlay__icon:hover .icon {
  transform: rotate(90deg);
}

.overlay__icon span {
  margin-top: 5px;
  font-size: 14px;
  line-height: 1;
  font-family: roboto-600, sans-serif;
  color: #585a59;
  text-transform: uppercase;
}

.overlay__icon .icon {
  -webkit-transition: all 0.35s ease;
  -moz-transition: all 0.35s ease;
  -ms-transition: all 0.35s ease;
  -o-transition: all 0.35s ease;
  transition: all 0.35s ease;
}

.overlay__text {
