  opacity: 0;
  transform: scale(0);
  background-color: transparent;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 10000;
  transition: visibility 0.5s, opacity 0.5s, transform 0.5s;
