.toggle-mnu {
  display: none;
  width: 50px;
  height: 50px;
  padding: 11px;
  cursor: pointer;
}

@media (max-width: 768px) {
  .toggle-mnu {
    display: block;
  }
}

.toggle-mnu span {
  position: relative;
  display: block;
  width: 100%;
  height: 2px;
  margin-top: 4px;
  background-color: #242626;
  transition: all 0.3s;
  backface-visibility: hidden;
  border-radius: 3px;
}

.toggle-mnu span::after, .toggle-mnu span::before {
  content: '';
  height: 2px;
  width: 100%;
  background-color: #242626;
  border-radius: 3px;
  backface-visibility: hidden;
  transition: all 0.3s;
  position: absolute;
  left: 0;
  top: 9px;
}

.toggle-mnu span::after {
  top: 18px;
}

.toggle-mnu.on span {
  background-color: transparent;
}

.toggle-mnu.on span::before {
  transform: rotate(45deg) translate(-1px, 0px);
}

.toggle-mnu.on span::after {
  transform: rotate(-45deg) translate(6px, -7px);
}

.hamburger {
  width: 60px;
  height: 60px;
  transition-duration: 0.5s;
  display: block;
  position: relative;
}

.hamburger span {
  position: relative;
  display: block;
  width: 27px;
  height: 2px;
  margin: 29px auto 0;
  background-color: #a5a5a5;
  border-radius: 15px;
  transition: all 0.3s;
  backface-visibility: hidden;
}

.hamburger span:hover {
  cursor: pointer;
}

@media (max-width: 425px) {
  .hamburger span {
    background-color: #9b9b9b;
  }
}

.hamburger span::after, .hamburger span::before {
  content: ' ';
  width: 100%;
  height: 2px;
  background-color: #a5a5a5;
  backface-visibility: hidden;
  border-radius: 15px;
  position: absolute;
  left: 0;
  top: 9px;
  transition: all 0.3s;
}

@media (max-width: 425px) {
  .hamburger span::after, .hamburger span::before {
    background-color: #9b9b9b;
  }
}

.hamburger span::before {
  top: -9px;
}

.hamburger span::after {
  top: 9px;
}

@media (min-width: 425px) {
  .hamburger.on span {
    transition: 0.5s;
  }
  .hamburger.on span::after, .hamburger.on span::before {
    width: 16px;
  }
  .hamburger.on span::before {
    transform: translate(-3px, 4px) rotate(-45deg);
  }
  .hamburger.on span::after {
    transform: translate(-3px, -4px) rotate(45deg);
  }
}

