.toggle-mnu {
  display: block;
  width: 50px;
  height: 50px;
  padding: 11px;
  cursor: pointer;
}

.toggle-mnu span {
  position: relative;
  display: block;
  width: 100%;
  height: 2px;
  margin-top: 4px;
  background-color: #242626;
  transition: all 0.3s;
  backface-visibility: hidden;
  border-radius: 3px;
}

.toggle-mnu span::after, .toggle-mnu span::before {
  content: '';
  height: 2px;
  width: 100%;
  background-color: #242626;
  border-radius: 3px;
  backface-visibility: hidden;
  transition: all 0.3s;
  position: absolute;
  left: 0;
  top: 9px;
}

.toggle-mnu span::after {
  top: 18px;
}

.toggle-mnu.on span {
  background-color: transparent;
}

.toggle-mnu.on span::before {
  transform: rotate(45deg) translate(-1px, 0px);
}

.toggle-mnu.on span::after {
  transform: rotate(-45deg) translate(6px, -7px);
}

