$card-indent: 50px;
$card-indent--mobile: 10px;

@mixin cardBoxMobile {
    margin-bottom: $card-indent;

    @include respond-to(sm) {
        margin-bottom: $card-indent--mobile;
        padding: 15px 10px;
        border-radius: $border-radius;
        background-color: #fff;
    }
}

.page-card {
    @include respond-to(xs) {

        .footer {
            padding-bottom: 44px;
        }
    }

    .card {
        padding-top: 0;
    }

    .header {
        position: static;
    }

    .slick-dots li {
        width: auto;
        height: auto;
        font-family: $f-light;
    }

    .slick-dotted.slick-slider {
        margin-bottom: 0;
    }
} //.page-card

.card {

    &__content {
        @include respond-to(sm) {
            background-color: $gray--lighten;

            .container {
                padding: 0;
            }

            .bb-accordeon__item {
                margin-top: 1px;
            }
        }

        @include respond-to(xs) {
            margin-bottom: 0;
        }

        &_inner {
            @include flex;
            position: relative;

            @include respond-to(sm) {
                flex-direction: column;
            }
        }

        &_left {
            width: calc(100% - 375px - 30px);

            @include respond-to(sm) {
                width: 100%;
            }
        } //.card__content_left

        &_right {
            width: 375px;
            margin-left: 30px;

            @include respond-to(sm) {
                width: 100%;
                margin-left: 0;
            }
        } //.card__content_right
    } //.card__content

    &__bottom {
        padding: 40px 0 60px;
        border-top: 1px solid $color-border--lightest;

        @include respond-to(sm) {
            margin-top: 0;
            padding: $gutter 0;
        }
    } //.card__bottom

    &__nearby {
        @include row-flex();

        .catalog-item {
            margin-bottom: 15px;
            @include col();
            @include size(4);
            @include size-sm(6);
            @include size-xs(12);
        }
    } //.card__nearby

    &__table {
        width: 100%;
        margin-bottom: $card-indent--mobile;
        text-align: center;
        font-size: 14px;
    } //.card__table

    &__similar {
        @include row-flex;

        .catalog-item--lk {
            margin-bottom: 0;
            @include col;
            @include size(4);

            .catalog-item {

                &__info {
                    flex-wrap: wrap;
                }

                &__desc {
                    display: block;
                    margin-top: $gutter/2;
                }

                &__who {
                    flex-wrap: wrap;

                    .num {
                        width: 100%;
                        text-align: right;
                    }
                }
            }

            .properties {
                margin-left: 0;
                width: 100%;

                &__list {
                    flex-wrap: wrap;
                }

                &__item {

                    &:first-child {

                        &::before {
                            display: none;
                        }
                    }
                }
            }

            .page-title {

                &__wrap {
                    min-height: initial;
                }
            }
        }
    } //.catalog__similar

    &--lk {

        .card {

            &__content {
                margin-bottom: $gutter/2;
            }
        }

        .card-request-form {

            &__top {
                margin-top: 0;
                padding-top: 0;
                border: none;
            }
        }

        .card-info {
            top: auto;

            &__user-phone {
                margin-bottom: 0;
            }
        }
    } //.card--lk

    .table {
        width: 100%;
        margin-bottom: $card-indent--mobile;
        text-align: center;
        font-size: 14px;
        @extend %box;

        &__head {
            border-bottom: 1px solid $color-border--lightest;
        } //.table__head

        &__cell {
            padding: 5px;
            border-right: 1px solid $color-border--lightest;

            &:last-child {
                border-right: none;
            }

            &--num {
                font-family: $f-num;
            }
        } //.table__cell
    } //.table

    .page-title {
        @include respond-to(xs) {
            margin: 0;
            font-size: 18px;
        }
    }

    .slick-slide {
        margin: 0 1px;

        @include respond-to(xs) {
            margin: 0;
        }
    }
} //.card

.card-title {
    position: relative;

    &__inner {
        margin-bottom: 30px;
        @include flexCenter;
        justify-content: space-between;
    } //.card-title__inner

    @include respond-to(sm) {
        margin-bottom: $card-indent--mobile;

        .page-title {
            margin-bottom: 0;
        }
    }

    .button-subscribe {
        margin-left: auto;
    }
} //.card-title

.card-about {
    font-family: $f-light;
    font-size: 15px;
    color: #000;
    background-color: #fff;
    position: relative;

    @include respond-to(sm) {
        margin-bottom: $card-indent--mobile;
        font-size: 14px;
        padding: 15px 10px;
        border-radius: $border-radius;
        background-color: #fff;
        position: relative;

        .button-link {
            font-size: 13px;
            line-height: 1;
        }
    }

    &__box {
        margin-bottom: 30px;

        @include respond-to(xs) {

            &:last-child {
                margin-bottom: 0;
            }
        }
    } //card-about__box

    .button-link {
        background-color: #fff;
        position: absolute;
        bottom: 4px;
        right: 8px;

        @include respond-to(xs) {
            font-size: 14px;
            bottom: 15px;
            right: 12px;
        }
    }
} //.card-about

.card-gallary {

    &__wrap {
        @include respond-to(sm) {
            margin-bottom: $card-indent--mobile;
        }

        @include respond-to(xs) {

            .container {
                padding: 0;
            }

            .bb-slider__arrow {
                display: none !important;
            }
        }
    } //.card-gallary__wrap

    &__dots {
        display: none;
        padding: 2px 5px 1px;
        font-family: $f-light;
        font-size: 11px;
        letter-spacing: 3px;
        color: #fff;
        border-radius: $border-radius * 2;
        position: absolute;
        top: 15px;
        right: 15px;
        background-color: rgba(#000, 0.55);

        @include respond-to(xs) {
            display: block;
        }

        ul {
            position: static;

            li {
                display: none;

                &.slick-active {
                    display: block;
                }
            }
        }
    } //.card-gallary__dots

    &__item {
        @include cover;
    } //.card-gallary__item
} //.card-gallary

.card-menu {
    display: flex;
    margin-right: 30px;

    @include respond-to(sm) {
        width: 100%;
        margin-right: 0;
    }

    &__wrap {
        margin: auto auto 15px;
        border-bottom: 1px solid $color-border--lightest;
        background-color: #fff;
        transition: background-color 0.2s ease;

        @include respond-to(sm) {
            margin-bottom: $card-indent--mobile;
        }

        &.is-sticky {
            margin-bottom: 0;
            background-color: $gray--darkest;

            @include respond-to(sm) {
                background-color: #fff;
                box-shadow: $bxs;
            }

            .card-menu {

                &__link {
                    color: #fff;

                    &:hover,
                    &:focus,
                    &:active {
                        color: $color-main;

                        @include respond-to(xs) {
                            color: $color-text;
                        }
                    }

                    @include respond-to(sm) {
                        color: initial;

                        &::before {
                            color: $gray--light;
                        }
                    }

                    &_see-gallary {
                        display: inline-block;
                    }
                }
            }
        }
    } //.card-menu__wrap

    &__inner {
        max-width: 1200px;
        margin: 0 auto;
        @include flexCenter;
    } //.card-menu__inner

    &__list {
        @include flexCenter;

        @include respond-to(sm) {
            width: 100%;
            text-align: center;
        }
    } //.card-menu__list

    &__item {
        height: 65px;
        line-height: 65px;
        position: relative;
        overflow: hidden;

        @include respond-to(sm) {
            width: 100%;
            height: 50px;
            line-height: 50px;
        }

        &.is-active {

            .card-menu__link {
                font-family: $f-bold;

                &::after {
                    display: block;
                }
            }
        } //.card-menu__item.is-active

        &_about {
            @include respond-to(sm) {
                display: none;
            }
        } //.card-menu__item_about

        &_see-gallary {
            @include max-screen(1024) {
                margin-left: 90px;
            }

            @include respond-to(sm) {
                margin-left: 0;

                &::before {
                    display: none;
                }
            }

            &::after {
                display: none !important;
            }
        } //.card-menu__item_see-gallary
    } //.card-menu__item

    &__link {
        display: inline-block;
        padding: 0 30px;
        font-family: $f-light;
        font-size: 15px;
        line-height: 65px;
        color: $color-text;
        position: relative;
        @include tr;

        @include respond-to(sm) {
            width: 100%;
            line-height: 50px;
        }

        @include respond-to(xs) {
            width: 100%;
            margin: auto;
            padding: 0;
            text-align: center;
        }

        &:hover,
        &:focus {
            color: $color-main;

            @include respond-to(xs) {
                color: $color-text;
            }
        }

        &::after {
            content: '';
            display: none;
            width: 100%;
            height: 3px;
            background-color: $color-main;
            position: absolute;
            left: 0;
            bottom: 0;
        }
    } //.card-menu__link
} //.card-menu

.card-info {
    border-radius: $border-radius;
    position: relative;
    top: -79px;
    z-index: 101;

    @include respond-to(sm) {
        background-color: #fff;
        top: auto;
        z-index: auto;
    }

    &__top {
        margin-bottom: $card-indent--mobile;
        padding: 15px;
        position: relative;
        overflow: hidden;
        @extend %box;
        @include flexColumn;

        @include respond-to(sm) {
            margin: 0;
            padding: 0;
            box-shadow: none;
            position: fixed;
            bottom: 0;
            left: 0;
            width: 100%;
            background-color: #fff;
            z-index: 99;
            overflow: initial;
            transition: all 0.3s cubic-bezier(0.25, 0.8, 0.4, 0.95);
        }

        &.is-booked {

            .card-info {

                &__btn {
                    @include btnChangeColor($color-accent, #fff);

                    @include respond-to(xs) {
                        display: block;
                    }

                    &::before {
                        display: block;
                    }
                }

                &__request,
                &__category {
                    display: flex;
                }

                &__policy {
                    display: block;

                    @include respond-to(sm) {
                        display: none;
                    }
                }
            }

            .button-animate {

                &__count {
                    display: block;
                }

                &__icon {
                    display: block;
                }
            }
        }

        &.is-disabled {

            .card-info__btn {
                background-color: $color-muted;
                pointer-events: none;
                cursor: default;
            }
        }
    } //.card-info__top

    &__header {

        &--mobile {
            padding: 12px 15px 11px;
            @extend %textLight;
            border-bottom: 1px solid $color-border--lightest;

            @include min-screen(769) {
                display: none;
            }
        } //.card-info__header--mobile
    } //.card-info__header

    &__inner {
        padding: 15px 40px 15px 10px;
        @extend %box;
        position: relative;
        display: flex;

        @include respond-to(sm) {
            margin-bottom: $card-indent--mobile;
            border-radius: $border-radius;
            box-shadow: none;
        }

        @include respond-to(xs) {
            padding: 15px 10px;
        }

        .user {
            display: flex;

            &__avatar {
                margin: 0;
            }

            &__img {
                width: 70px;
                height: 70px;

                @include respond-to(xs) {
                    width: 55px;
                    height: 55px;
                }
            }
        } //.card-info__inner  .user

        .list {

            &__icon {
                margin-right: 5px;
                font-size: 13px;

                &.fa-clock {
                    margin-left: 1px;
                    margin-right: 6px;
                }
            }
        } //.card-info__inner .list
    } //.card-info__inner

    &__btn {
        display: block;
        width: 100%;
        padding: 10px 35px;
        text-transform: uppercase;
        font-family: $f-medium;
        position: relative;

        @include respond-to(xs) {
            display: none;
        }

        .button-animate {

            &__icon {
                display: none;
                font-size: 20px;
            }

            &__count {
                display: none;
                margin-left: auto;
                font-family: $f-medium;
            }
        } //.card-info__btn .button-animate__count

        @include respond-to(sm) {
            border-radius: 0;
            font-size: 18px;
        }

        &.is-attention {

            .card-info__attention {
                @include trfY(0);
            }
        }
    } //.card-info__btn

    &__attention {
        text-align: center;
        padding: 11px 15px 0;
        text-transform: initial;
        font-family: $f-light;
        font-size: 14px;
        color: #fff;
        background-color: $color-danger--light;
        @include tr;
        @include coverdiv;
        z-index: 99;
        @include trfY(-100%);

        &--lg {
            margin-top: 10px;
            text-align: center;
            padding: 15px;
            font-size: 14px;
            color: $gray--darkest;
            background-color: #fbf6e0;
            position: relative;
            @include trfY(0);

            @include respond-to(sm) {
                display: none;
            }
        }
    } //.card-info__attention

    &__request {
        display: none;

        @include respond-to(md) {
            max-height: 100%;
            overflow-y: auto;
            @include bb-scroll;
            margin-top: 0;
            position: fixed;
            top: 0;
            bottom: 47px;
            left: 0;
            width: 100%;
            z-index: 101;
            background-color: #fff;
            box-shadow: none;
            transform: translateY(200%);
            display: flex;
            flex-direction: column;
            @include tr;

            &.is-open {
                transform: translateY(0);
            }
        }

        .bb-date {
            margin-right: 25px;
        }

        .bb-input,
        .bb-date {
            margin-bottom: 20px;
            width: 50%;
        }

        .bb-input--user,
        .bb-input--phone,
        .bb-input--select {
            width: 100%;
            margin-bottom: 20px;

            @include respond-to(sm) {
                margin-bottom: 15px;
            }

            input {
                width: 100%;
            }
        }
    } //.card-info__request

    &__fav {
        position: absolute;
        top: 10px;
        right: 10px;

        @include respond-to(sm) {
            display: none;
        }

        &--mobile {
            display: none;
            top: -10px;
            right: 5px;

            @include respond-to(sm) {
                display: block;
            }
        }
    } //.card-info__fav

    &__link {
        margin-top: 15px;
        padding-top: 12px;
        font-size: 14px;
        border-top: 1px solid $color-border--lightest;

        .btn-link {
            text-align: left;
            white-space: nowrap;
        }
    } //.card-info__link

    &__desc {
        width: 100%;
        margin-left: 15px;

        @include respond-to(xs) {
            margin-left: 10px;
        }
    } //.card-info__desc

    &__title {
        display: block;
        font-family: $f-medium;
        font-weight: 500;
        font-size: 18px;
        color: $gray--darkest;
    } //.card-info__title

    &__avatar {
        display: inline-block;
    } //.card-info__avatar

    &__address {
        font-size: 13px;
    } //.card-info__address

    &__rating {
        @include flexCenter;
        margin-bottom: 15px;

        @include respond-to(sm) {
            display: none;
        }

        &--mobile {
            display: none;

            @include respond-to(sm) {
                display: block;
            }
        }

        .rating {
            margin-right: 0;

            &__wrap {
                padding-left: 27px;
                position: relative;
            }

            &__data {
                position: absolute;
                top: 45%;
                transform: translateY(-50%);
                left: 0;

                strong {
                    margin-bottom: 0;
                    font-size: 28px;
                }

                span {
                    font-size: 16px;
                }
            }

            &__reviews {
                font-size: 13px;
            }
        }
    } //.card-info__rating

    &__go-reviews {
        margin-left: auto;
        font-family: $f-light;
        font-size: 15px;
    } //.card-info__go-reviews

    &__category {
        display: none;

        @include respond-to(sm) {
            padding: 10px 15px 15px;
            border-bottom: 1px solid $color-border--lightest;
            order: 1;
        }
    } //.card-info__category

    &__user-phone {
        margin-bottom: 15px;

        .user__time {
            display: block;
            @extend %textLight;
            font-size: 13px;
            color: $gray--light;
        }
    } //.user-phone

    &__policy {
        display: none;
        text-align: center;
        font-size: 12px;
        line-height: 1.2;

        @include respond-to(sm) {
            display: none;
        }

        a {
            font-size: 12px;
            color: $gray--light;
            text-decoration: underline;

            &:hover {
                text-decoration: none;
            }
        }
    } //.user__policy
} //.card-info

.card-request-form {
    width: 100%;

    @include respond-to(sm) {
        padding: 15px;
        order: 2;
    }

    @include min-screen(769) {

        .selectize-dropdown {
            max-height: 140px;
            overflow: hidden;
        }
    }

    &.is-success {

        .card-request-form {

            &__success {
                @include trfY(0);
            }

            &__request {
                overflow-y: hidden;
            }
        }
    } //.card-request-form.is-success

    &.is-confirm {

        .card-request-form {

            &__top,
            &__sucsess {
                display: none !important;
            }

            &__confirm {
                display: block;
            }
        }
    } //.card-request-form.is-confirm

    &__inner {
        @include flex;
    } //.card-request-form__inner

    &__title {
        @extend %titleSmallBold;

        @include min-screen(769) {
            display: none;
        }
    } //.card-request-form__title

    &__top {
        margin-top: 15px;
        padding-top: 13px;
        border-top: 1px solid $color-border--lightest;

        @include respond-to(sm) {
            margin-top: 0;
            padding-top: 0;
            border-top: none;
        }
    } //.card-request-form__top

    &__close {
        width: 32px;
        height: 32px;
        text-align: center;
        fill: $gray--light;
        background-color: transparent;
        border: none;
        cursor: pointer;
        position: absolute;
        top: 7px;
        right: 10px;
        align-items: center;
        justify-content: center;
        display: none;

        @include respond-to(sm) {
            display: flex;
        }

        .fa {
            font-size: 20px;
        }

        .icon {
            width: 18px;
            height: 18px;
        }
    } //.card-request-form__close

    &__success {
        padding: 30px;
        text-align: center;
        background-color: #fff;
        @include coverdiv;
        z-index: 260;
        @include tr;
        @include trfY(-200%);
        will-change: transform;
        display: flex;
        justify-content: center;

        @include respond-to(sm) {
            padding: 15px;
        }

        div {
            display: block;
            margin: auto;
            font-family: $f-light;
            font-size: 14px;
            color: $gray--light;
        }

        span {
            display: block;
            margin-bottom: 5px;
            font-family: $f-bold;
            text-transform: uppercase;
            font-size: 16px;
            color: $gray;
        }

        small {
            display: block;
            font-size: 14px;
        }

        .button-animate {
            display: block;
            max-width: 185px;
            margin: 20px auto 10px;
            padding: 5px 25px;
            font-family: $f-light;
            font-size: 15px;
        }

        .button-link {
            display: block;
        }
    } //.card-info__success

    &__confirm {
        display: none;
        margin-top: 25px;
        padding-top: 13px;
        text-align: center;
        border-top: 1px solid $color-border--lightest;

        @include respond-to(sm) {
            border-top: none;
        }
    } //.card-info__confirm
} //.card-request-form

.card-services {
    margin-bottom: $card-indent;

    @include respond-to(sm) {
        margin-bottom: $card-indent--mobile;
    }

    &__items {

        .bb-accordeon__title {
            @include max-screen(375) {
                padding: 15px 10px;
            }
        }
    }

    .page-title {

        &__wrap {
            @include respond-to(sm) {
                display: none;
            }
        }

        &--small {
            @include respond-to(sm) {
                display: none;
            }
        }
    }

    .bb-accordeon {

        &__title {
            @include respond-to(xs) {
                padding: 18px 13px;
            }
        }
    }
} //.card-services

.card-services-item {
    width: 100%;
    padding: 15px 20px;
    @include flexCenter;
    font-size: 15px;
    color: #000;
    border-bottom: 1px solid $color-border--lightest;
    cursor: pointer;

    &:last-child {
        border-bottom: 0;
    }

    @include respond-to(md) {
        flex-wrap: wrap;
    }

    @include respond-to(sm) {
        flex-wrap: nowrap;
    }

    @include respond-to(xs) {
        padding: 15px 10px;
        flex-wrap: wrap;
    }

    @include max-screen(375) {
        padding: 10px;
    }

    &:hover {
        background-color: $color-bg--lighten;
    }

    &.is-booked {
        @include min-screen(769) {

            &:hover {

                .card-services-item__action {

                    &_done {
                        display: none !important;
                    }

                    &_del {
                        display: block;
                    }
                }
            }
        }
    } //.card-services-item.is-booked

    &__top {
        display: flex;
        justify-content: space-between;
    } //.card-services-item__top

    &__left {
        min-width: 190px;
        padding-left: 20px;
        position: relative;
        @include flexColumn;

        @include respond-to(xs) {
            padding-left: 25px;
            width: auto;
        }
    } //.card-services-item__left

    &__middle {
        margin-left: 30px;
        @include flexCenter;
        flex: 1;

        @include max-screen(1024) {
            margin-left: 0;
            margin-bottom: 5px;
            width: 100%;
            order: 3;
            justify-content: center;
        }

        @include respond-to(sm) {
            margin-left: 30px;
            margin-bottom: 0;
            order: initial;
        }

        @include respond-to(xs) {
            margin: 10px 0 5px;
            order: 3;
        }
    } //.card-services-item__middle

    &__bottom {
        display: flex;
        margin-top: 10px;
        margin-bottom: 10px;
        margin-left: 20px;
    } //.card-services-item__bottom

    &__right {
        margin-left: auto;
        @include flexCenter;
        flex-shrink: 0;

        @include respond-to(xs) {
            margin-left: auto;
        }
    } //.card-services-item__right

    &__title {

        span {

            & + span {
                margin-left: 10px;

                &::before {
                    @include fa('regular');
                    content: '\f067';
                    font-size: 14px;
                    color: $color-main;

                    margin-right: 10px;
                }
            }
        }
    } //.card-services-item__title

    &__box {

        .button-link {
            margin-right: 10px;
        }
    } //.card-services-item__box

    &__example {
        width: 65px;

        & + .card-services-item__example {
            margin-left: 1px;
        }
    } //.card-services-item__example

    &__action {
        @include flexCenter;

        &_add {

            .card-services-item__btn {

                &::before {
                    content: '\f067';
                }
            }
        } //.card-services-item__action_add

        &_done {
            display: none;

            .card-services-item {

                &__text {
                    color: $gray--light;
                    font-family: $f-light-i;
                    font-size: 13px;
                }

                &__btn {
                    background-color: $color-success--light;

                    &::before {
                        content: '\f00c';
                        color: #fff;
                    }

                    &:hover {

                        &::before {
                            color: #fff;
                        }
                    }
                }
            }
        } //.card-services-item__action_done

        &_del {
            display: none;

            .card-services-item {

                &__text {
                    color: $gray--light;
                    font-family: $f-light-i;
                    font-size: 13px;
                }

                &__btn {
                    border: 2px solid $color-main;
                    box-shadow: none;

                    &::before {
                        content: '\f068';
                    }
                }
            }
        } //.card-services-item__action_del
    } //.card-services-item__action

    &__time {
        font-family: $f-light;
        font-size: 14px;
        color: $color-text--light;
    } //.card-services-item__time

    &__info-block {
        position: absolute;
        top: 2px;
        left: -4px;
    } //.card-services-item__info-block

    &__text {
        margin-top: 3px;
        margin-right: 15px;
        font-family: oswald-400, sans-serif;
        font-size: 16px;
        color: #000;

        span {
            margin-right: 5px;
            font-family: oswald-300, sans-serif;
            color: $gray--light;
        }
    } //.card-services-item__text

    &__btn {
        display: inline-flex;
        width: 25px;
        height: 25px;
        @extend %box;
        border-radius: 50%;

        &::before {
            margin: auto;
            @include fa('regular');
            font-size: 13px;
            line-height: 1;
            color: $color-main;
            position: relative;
            top: 1px;
            @include tr;
        }

        &:hover {

            &::before {
                color: lighten($color-main, 8%);
            }
        }
    } //.card-services-item__btn

    &--combo {
        display: block;
    } //.card-services-item--combo
} //.card-services-item

.card-adress {
    margin-bottom: $card-indent;

    @include respond-to(sm) {
        margin-bottom: $card-indent--mobile;
    }

    &__map {
        padding-bottom: 33.33%;
        position: relative;
        @extend %box;

        @include respond-to(xs) {
            padding-bottom: 56.25%;
        }

        #card-map {
            @include coverdiv;
        }
    } //.card-adress__map

    &__working {
        @include cardBoxMobile;
    } //.card-adress__working

    &__place {
        margin-bottom: $gutter;

        @include respond-to(sm) {
            margin-bottom: 0;
            padding: 15px 10px;
            background-color: #fff;
            border-radius: $border-radius $border-radius 0 0;
        }

        .place-text {
            font-size: 15px;
            line-height: 1;

            &__box {

                &:last-child {
                    text-align: right;
                }
            }
        }

        .icon-location {
            width: 18px;
            height: 18px;
            fill: $color-main;
            @include vy;
            left: 0;
        }
    } //.card-adress__place
} //.card-adress

.card-reviews {
    margin-bottom: $card-indent;
    background-color: #fff;

    @include respond-to(sm) {
        padding: 15px 10px;
        margin-bottom: $card-indent--mobile;
    }
} //.card-reviews

.reviews {

    &__header {
        @include flexCenter;
        margin-bottom: $card-indent;

        @include respond-to(sm) {
            margin-bottom: $card-indent--mobile;
            padding: 0;
            flex-direction: column;
            align-items: flex-start;
        }

        &_left {
            flex-basis: 20%;
            margin-right: 30px;

            @include respond-to(sm) {
                display: flex;
                align-items: center;
                width: 100%;
                margin-right: 0;
                margin-bottom: 15px;

                .button-link {
                    margin-left: auto;
                    font-size: 14px;
                }
            }
        }

        //.reviews__header_left

        &_right {
            width: calc(100% - 20% - 30px);

            @include respond-to(sm) {
                width: 95%;
            }

            @include respond-to(xs) {
                width: 100%;
            }

            .rating {

                &__wrapper {
                    margin-bottom: 7px;
                }
            }
        }

        //.reviews__header_right
    } //.reviews__header

    &__rating {
        @include flexCenter;
        flex-direction: column;

        @include respond-to(sm) {
            flex-direction: row;
            flex-wrap: wrap;
        }

        .rating {

            &__reviews {
                text-align: center;
            }

            @include respond-to(sm) {
                margin: 0;

                &__data {

                    strong {
                        font-size: 35px;
                    }

                    span {
                        display: none;
                    }
                }
            }
        }
    } //.reviews__rating

    &__message {
        padding: 20px 15px;
        border-bottom: 1px solid $color-border--lightest;

        @include respond-to(xs) {
            padding: 10px 0;
        }
    } //.reviews__message

    &__title {
        margin-bottom: $card-indent--mobile;
    } //.reviews__title

    &__name {
        margin-left: 10px;
        font-size: 14px;
        color: #000;
    } //.reviews__name

    &__time {
        margin-left: auto;
        font-family: $f-light;
        font-size: 13px;
        font-style: italic;
        color: $color-text--light;
    } //.reviews__time

    &__text {
        font-family: $f-light;
        font-size: 13px;
        color: $gray--darkest;
    } //.reviews__text

    &__buttons {
        text-align: center;
    } //.reviews__buttons

    &__btn {
        padding: 20px;
        font-family: $f;
        // text-transform: uppercase;
        font-size: 14px;
    } //.reviews__btn

    &__empty {
        @include flexCenter;

        span {
            margin: auto;
            @extend %textLight;
            font-size: 15px;
        }
    } //.reviews__empty
} //.reviews

.user-phone {
    font-family: $f-num--m;
    font-size: 16px;

    &__btn {
        padding: 0;
    } //.user-phone__btn
} //.user-phone

.more-services {
    margin-bottom: $card-indent;
    background-color: #fff;

    @include respond-to(sm) {
        margin-bottom: $card-indent--mobile;
        padding: 15px 10px 10px;
    }

    .page-title {

        &--small {
            margin-top: 0;
        }
    }
} //.more-services
