@keyframes spin {

    0% {
        transform: translate(-50%, -50%) rotate(0deg) scale(1);
    }

    90% {
        transform: translate(-50%, -50%) rotate(1080deg) scale(1);
    }

    100% {
        transform: scale(0);
    }
}

@keyframes success {

    from {
        transform: translate(-50%, -50%) rotate(-45deg) scale(0);
    }

    to {
        transform: translate(-50%, -50%) rotate(0) scale(1);
    }
}

@keyframes btsStatusAnimation {

    from {
        transform: translateY(150%);
    }

    to {
        transform: translate(0);
    }
}

@keyframes vibrate {

    0%,
    30%,
    60%,
    85%,
    100% {
        left: 0;
        background-color: $color-danger;
    }

    10%,
    40%,
    90%,
    70% {
        left: -2px;
        background-color: $color-danger;
    }

    20%,
    50%,
    80%,
    95% {
        left: 2px;
        background-color: $color-danger;
    }
}

@mixin resetBtn {
    border: 0;
    background-color: transparent;
    cursor: pointer;
    outline: none;
    overflow: hidden;
}

%button {
    padding: 9px 35px;
    font-family: $f-medium;
    font-size: 15px;
    letter-spacing: 0.3px;
    text-align: center;
    color: #fff;
    text-decoration: none;
    border: none;
    border-radius: $border-radius;
    transition: all 0.12s linear;
    cursor: pointer;
    outline: none;
    overflow: hidden;
    position: relative;
    z-index: z(default);
    display: inline-flex;
    justify-content: center;

    @include respond-to(xs) {
        font-size: 16px;
    }
} //tamplate btn

@mixin btnChangeColor($btnBg, $btnText) {
    color: $btnText;
    background-color: $btnBg;

    &:hover {
        color: $btnText;
        background-color: $btnBg;

        .button-animate__hover {
            background-color: rgba(255, 255, 255, 0.16);
        }
    }

    .button-animate__hover {
        background-color: rgba(255, 255, 255, 0.36);
    }
} //Change Bg & Color button-animate

@mixin btnTrs($btnBg, $btnText) {
    color: $btnBg;
    border: 1px solid $btnBg;
    background-color: transparent;
    @include tr;

    &:hover {
        color: $btnText;
        background-color: transparent;

        .button-animate__hover {
            background-color: $btnBg;
        }
    }

    .button-animate__hover {
        background-color: $btnBg;
    }
} //Change Bg & Color button-animate transparent

@mixin btnBg($bgc) {
    background-color: $bgc;
    box-shadow: 0 1px 10px $bgc;

    &:hover,
    &:focus,
    &:active {
        background-color: lighten($bgc, 10%);
        box-shadow: 0 1px 10px lighten($bgc, 10%);
    }
}

.button {
    @extend %button;

    @each $theme, $color in $button-colors {

        &--#{$theme} {
            background-color: $color;
            color: #fff;
        }

        &--#{$theme}:hover,
        &--#{$theme}:focus {
            background-color: lighten($color, 10%);
            color: #fff;
        }

        &--#{$theme}:active {
            background-color: lighten($color, 7%);
        }
    }

    &-subscribe {
        padding: 5px 25px;
    } //.button-subscribe

    &-icon {
        width: 32px;
        height: 32px;
        padding: 0;
        color: $gray--lighten;
        background-color: transparent;
        border: none;
        outline: none;
        position: relative;
        display: inline-flex;
        align-items: center;

        &::before {
            margin: auto;
            @include fa('solid');
            font-size: 23px;
            line-height: 23px;
            @include vcenter;
        }

        &--fav {

            &::before {
                content: '\f004';
                @include fa('light');
            }

            &.is-checked {

                &::before {
                    @include fa('solid');
                    color: rgba($color-main, 0.65);
                }
            }
        } //.button-icon--fav

        &--info {

            &:hover,
            &.is-active {

                &::after {
                    animation: none;
                }
            }

            &::before {
                content: '\f0f3';
                font-size: 20px;
                color: $gray;
            }

            &::after {
                content: attr(data-count);
                @extend %badge;
                color: #000;
                background-color: $color-main;
                border: 2px solid #fff;
                position: absolute;
                top: 6px;
                left: 23px;
                animation: pulse 1.5s infinite;
                @include respond-to(xs) {
                    animation: none;
                }
                @include respond-to(i5) {
                    left: 16px;
                }
            }

            &.is-checked {

                &::before {
                    color: $gray;
                }
            }
        } //.button-icon--info

        &--search {

            &::before {
                content: '\f002';
                font-size: 25px;
                color: $gray;
            }
        } //.button-icon--search
    } //.button-icon

    &-social {
        width: 100%;
        text-transform: uppercase;
        font-size: 14px;

        &::before {
            @include fa('brand');
            font-size: 18px;
            line-height: 1;
        }

        &:hover {
            color: #fff;
        }

        &--whatsapp {
            background-color: #23d366;

            &::before {
                content: '\f232';
            }

            &:hover {
                background-color: darken(#23d366, 10%);
            }
        } //.button-social--whatsapp

        &--viber {
            background-color: #7d3daf;

            &::before {
                content: '\f409';
            }

            &:hover {
                background-color: darken(#7d3daf, 10%);
            }
        } //.button-social--viber

        &--vk {
            background-color: #4a76a8;

            &::before {
                content: '\f189';
            }

            &:hover {
                background-color: darken(#4a76a8, 10%);
            }
        } //.button-social--vk

        &--instagram {
            transition: opacity 0.7s ease-in-out;
            background: linear-gradient(
                135deg,
                #4c68d7 0%,
                #d321a4 50%,
                #ffef63 100%
            );

            &::before {
                content: '\f16d';
            }

            &::after {
                content: '';
                display: block;
                background: linear-gradient(
                    135deg,
                    #ffef63 0%,
                    #d321a4 50%,
                    #4c68d7 100%
                );
                @include coverdiv;
                opacity: 0;
                @include tr;
                z-index: z(down);
            }

            &:hover {

                &::after {
                    opacity: 1;
                }
            }
        } //.button-social--instagram
    } //.button-social

    &-text--up {
        text-transform: uppercase;
        font-family: $f-medium;
        font-size: 15px;
    } //.button--up

    &--small {
        padding: 4px 25px 5px;
        @extend %textLight;
        font-size: 14px;
    } //.button--small
} //.button

.button-animate {
    @extend %button;
    background-color: $color-main;

    &:hover,
    &:active {
        color: #fff;
        background-color: $color-main;

        .button-animate__hover {
            width: 800px;
            height: 800px;
            background-color: rgba(255, 255, 255, 0.16);
        }
    }

    @each $theme, $color in $button-colors {

        &.button--#{$theme} {
            background-color: $color;

            &:hover {
                background-color: $color;
            }
        }
    }

    &__hover {
        display: block;
        width: 0;
        height: 0;
        border-radius: 50%;
        background-color: rgba(255, 255, 255, 0.36);
        position: absolute;
        transform: translate(-50%, -50%);
        transition: width 1s, height 1s;
        z-index: z(down);
    } //.button-animate__hover

    &__icon {
        @include vy;
        left: 15px;

        &::after {
            width: 14px;
            height: 14px;
            border-radius: 500px;
            background-color: #000;
            content: attr(data-count);
            text-align: center;
            font-family: $f-num--l;
            font-size: 8px;
            line-height: 13px;
            color: #fff;
            position: absolute;
            top: -6px;
            right: -6px;
        }
    } //.button-animate__icon

    &__count {
        @include vy;
        right: 15px;

        .fa {
            font-size: 14px;
        }
    } //.button-animate__icon

    &--transparent {
        padding: 7px 35px;
        @include btnTrs($color-main, #fff);
    } //.button-animate--transparent

    &--white {
        padding: 6.5px 35px;
        @include btnTrs(#fff, #000);
    } //.button-animate--white

    &--black {
        @include btnTrs($color-btn--darken, #fff);
    } //.button-animate--black

    &--gray {
        @include btnTrs($gray, #fff);
        color: #000;
    } //.button-animate--gray

    &--muted {
        color: #fff;
        border-radius: $border-radius;
        background-color: $color-muted;
        @include tr;

        &:hover,
        &:focus,
        &:active {
            color: #fff;
            background-color: $color-muted;

            .button-animate__hover {
                background-color: lighten($color-muted, 2%);
            }
        }

        .button-animate__hover {
            background-color: $color-muted;
        }
    } //.button-animate--muted

    &--grayN {
        color: #fff;
        border-radius: $border-radius;
        background-color: $gray;
        @include tr;

        &:hover,
        &:focus,
        &:active {
            color: #fff;
            background-color: $gray;

            .button-animate__hover {
                background-color: lighten($gray, 2%);
            }
        }

        .button-animate__hover {
            background-color: $gray;
        }
    } //.button-animate--gray

    &--icon {
        display: inline-flex;
        @include respond-to(xs) {
            padding: 0;
            background-color: transparent;
        }

        .icon {
            margin: auto;
            width: 18px;
            height: 18px;
            fill: $color-main;
            @include min-screen(480) {
                display: none;
            }
        }

        .button-animate__text {
            @include respond-to(xs) {
                display: none;
            }
        }
    } //.button-animate--icon
} //.button-animate

.button-link {
    padding: 0;
    font-family: $f-light;
    font-size: 14px;
    color: $color-main;
    border: none;
    background-color: transparent;
    outline: none;
    cursor: pointer;

    &:hover,
    &:focus,
    &:active {
        text-decoration: underline;
        color: lighten($color-main, 3%);
    }

    .icon {
        display: inline-block;
        vertical-align: middle;
        margin-right: 5px;
        width: 15px;
        height: 15px;
    }

    span {
        display: inline-block;
        vertical-align: middle;
    }

    &--add {

        &:hover,
        &:focus {
            text-decoration: none;
        }
    }

    &--icon {

        &:hover {
            text-decoration: none;
        }
    }

    &--share {
        color: $gray--light;
    }

    @each $theme, $color in $button-colors {

        &--#{$theme} {
            color: $color;

            .icon {
                fill: $color;
            }
        }

        &--#{$theme}:hover,
        &--#{$theme}:focus {
            color: lighten($color, 10%);

            .icon {
                fill: lighten($color, 7%);
            }
        }

        &--#{$theme}:active {
            color: lighten($color, 7%);

            .icon {
                fill: lighten($color, 7%);
            }
        }
    }
} //.button-link

.btn {
    @extend %button;
    box-shadow: 0 2px 5px 0 #b7b7b7;

    &:hover {
        @include trfY(-1px);
        box-shadow: 0 3px 5px 0 #b7b7b7;
    }

    @each $theme, $color in $button-colors {

        &-#{$theme} {
            color: #fff;
            background-color: $color;

            &:hover {
                color: #fff;
                background-color: darken($color, 3%);
            }

            &:active {
                background-color: darken($color, 7%);
                box-shadow: none;
            }
        }
    }

    &__title {
        @include tr;
    }

    &-white {
        background: transparent;
        border: 2px solid #fff;
        box-shadow: none;
        color: #fff;

        &:hover {
            color: #fff;
            background-color: transparent;
            box-shadow: none;
            transform: none;

            @include respond-min(md) {
                background-color: rgba(255, 255, 255, 0.3);
            }
        }

        &:active {
            background-color: transparent;
            box-shadow: none;
            transform: none;
        }
    } //.btn--link

    &-link {
        padding: 0;
        font-family: $f;
        font-size: 14px;
        line-height: 1;
        letter-spacing: 0;
        border: 0;
        box-shadow: none;
        background-color: transparent;
        color: $color-main;
        cursor: pointer;
        outline: none;
        @include flexCenter;

        &:hover {
            color: darken($color-main, 8%);
            text-decoration: underline;
            box-shadow: none;
            @include trfY(0);
        }

        i {
            margin-top: -2px;
            margin-right: 7px;
            line-height: 1;
        }

        @each $theme, $color in $button-colors {

            &-#{$theme} {
                color: $color;
                cursor: pointer;

                &:hover {
                    color: darken($color, 7%);
                }

                &:active {
                    color: darken($color, 10%);
                }
            }
        }

        input[type='file'] {
            display: none;
        }
    } //.btn--link

    &-outline {
        @each $theme, $color in $button-colors {

            &-#{$theme} {
                padding: 8px 35px;
                color: $color;
                background-color: transparent;
                background-image: none;
                border: 1px solid $color;
                box-shadow: 0 2px 5px 0 lighten($color, 20%);

                &:hover {
                    box-shadow: 0 3px 5px 0 lighten($color, 20%);
                }

                &:active {
                    border-color: darken($color, 5%);
                    box-shadow: none;
                    @include trfY(0);
                }
            }
        }

        &-muted {
            color: $color-text;
            border-color: lighten(#d6d6d6, 11%);
            box-shadow: 0 2px 5px 0 #d6d6d6;

            &:hover {
                box-shadow: 0 3px 5px 0 #d6d6d6;
            }

            &:active {
                border-color: lighten(#d6d6d6, 10%);
                box-shadow: none;
            }
        }

        &-white {

            &:hover {
                color: #000;
            }
        } //.btn-outline
    } //.btn-outline

    &-social {
        min-width: 92px;
        padding: 11px 35px;
        text-transform: uppercase;
        font-size: 14px;

        &::before {
            @include fa('brand');
            font-size: 18px;
            line-height: 1;
        }

        &:hover {
            color: #fff;
        }

        &-text {
            text-transform: initial;

            &::before {
                margin-right: 10px;
            }
        }

        @each $theme, $color in $btn-social-color {

            &--#{$theme} {
                background-color: $color;

                &:hover {
                    background-color: darken($color, 10%);
                }
            }
        }

        @each $theme, $icon in $btn-social-icon {

            &--#{$theme} {

                &::before {
                    content: $icon;
                }
            }
        }

        span {
            margin-top: 2px;
            margin-left: 10px;
            font-family: $f;
            font-size: 15px;
            line-height: 1;
            text-transform: initial;
        }

        .text {
            display: none;
        }

        &-link {
            box-shadow: none;
            border: 0;

            @each $theme, $color in $btn-social-color {

                &--#{$theme} {
                    color: $color;

                    &:hover {
                        color: darken($color, 10%);
                    }
                }
            }

            @each $theme, $icon in $btn-social-icon {

                &--#{$theme} {

                    &::before {
                        content: $icon;
                    }
                }
            }

            span {
                font-family: $f-medium;
            }
        }

        &--allone {
            width: 100%;
            margin-left: 0;

            .text {
                display: block;
            }
        } //btn-social--allone

        &--instagram {
            transition: opacity 0.7s ease-in-out;
            background: linear-gradient(
                135deg,
                #4c68d7 0%,
                #d321a4 50%,
                #ffef63 100%
            );

            &::before {
                content: '\f16d';
            }

            &::after {
                content: '';
                display: block;
                background: linear-gradient(
                    135deg,
                    #ffef63 0%,
                    #d321a4 50%,
                    #4c68d7 100%
                );
                @include coverdiv;
                opacity: 0;
                @include tr;
                z-index: z(down);
            }

            &:hover {

                &::after {
