                }
            }
        } //btn-social--instagram

        &-outline {
            background-color: transparent;
            box-shadow: none;

            @each $theme, $color in $btn-social-color {

                &--#{$theme} {
                    border: 1px solid $color;

                    &::before {
                        color: $color;
                    }
                }
            }

            @each $theme, $icon in $btn-social-icon {

                &--#{$theme} {

                    &::before {
                        content: $icon;
                    }
                }
            }
