        height: 32px;
        padding: 0;
        color: $gray--lighten;
        text-align: center;
        background-color: transparent;
        border: none;
        outline: none;
        position: relative;
        display: inline-flex;
        align-items: center;

        &::before {
            margin: auto;
            @include fa('solid');
            font-size: 23px;
            line-height: 23px;
            @include vcenter;
        }

        i {
            margin: auto;
            font-size: 21px;
            color: $color-icon;
        }

        .icon {
            margin: auto;
        }

        @each $theme, $icon in $btn-icon {

            &--#{$theme} {

                &::before {
                    content: $icon;
                    @include fa('light');
                    color: $gray;
                }
            }
        }

        @each $theme, $color in $button-colors {

            &--#{$theme} {

                &::before {
                    color: $color;
                }
            }

            &.bg--#{$theme} {
                background-color: $color;

                &:hover {
                    background-color: lighten($color, 5%);
                }

                i,
                span {
                    color: #fff;
                }

                .icon {
                    fill: #fff;
                }
            }
        }

        &--fav {

            &::before {
                color: $color-icon--light;
            }

            &:hover {

                &::before {
                    color: rgba($color-main, 0.65);
                }
            }

            &.is-checked {

                &::before {
                    @include fa('solid');
                    color: rgba($color-main, 0.65);
                }
            }
        } //.btn-icon--fav

        &--info {

            &:hover,
            &.is-active {

                &::after {
                    animation: none;
                }
            }

            &::before {
                font-size: 20px;
                color: $gray;
            }

            &::after {
                content: attr(data-count);
                @extend %badge;
                color: #000;
                background-color: $color-main;
                border: 2px solid #fff;
                position: absolute;
                top: 6px;
                left: 23px;
                animation: pulse 1.5s infinite;
                @include respond-to(xs) {
                    animation: none;
                }
                @include respond-to(i5) {
                    left: 16px;
                }
            }
