        &--large {
            width: 56px;
            height: 56px;

            i,
            span {
                font-size: 20px;
            }

            .icon {
                width: 20px;
                height: 20px;
            }
        } //.btn-icon--large

        input[type='file'] {
            display: none;
        }
    } //.btn-icon

    &-animate {
        overflow: hidden;

        padding: 0;
        height: 40px;
        min-width: 180px;

        &.is-animate {

            &::after {
                content: '';
                display: block;
                @include coverdiv;
                background: transparent url('../img/general/loader-3.gif')
                    no-repeat center center;
                background-size: contain;
            }

            &::before {
                @include trfY(150%);
            }

            .btn {

                &__title {
                    display: none;
                }
            }
        }

        &:not(.is-ready) {

            &.is-error,
            &.is-success {

                &::before {
                    color: #fff;
                    @include fa('light');
                    font-size: 22px;
                    line-height: 1;
                    text-align: center;
                    margin-left: -11px;
                    margin-top: -10px;
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    animation: btsStatusAnimation ease-in 0.3s forwards;
                }

                .btn {

                    &__title {
                        @include trfY(-150%);
                    }
                }
            }

            &.is-success {

                &::before {
                    content: '\f00c';
                }
            }

            &.is-error {
                background-color: $color-danger;

                &::before {
                    content: '\f06a';
                }
            }
        }
    } //.btn-animate

    &__error {
        display: none;
        font-family: $f-light;
        font-size: 13px;
        line-height: 1;
        text-align: center;
        @include text-overflow;
        color: red;
        position: absolute;
        top: 104%;
        left: 0;
        right: 0;
        width: 100%;
    } //.btn__error

    &__list {
        color: $color-text;
        font-size: 14px;
        background-color: #fff;
        box-shadow: $bxs-box;
        margin: 1px;
        opacity: 0;
        visibility: hidden;
        position: absolute;
        top: 100%;
        left: 0;
        right: 0;
        transition: $trsMenu;
        @include flexColumn;
    } //.btn__list

    &__action {

        &--sub {
            padding: 10px;
            border: 0;
            background-color: transparent;
            box-shadow: none;
            outline: none;

            &:hover {
                color: $gray--darkest;
                background-color: $color-bg--light;
            }
        }
    } //.btn__action

    &--small {
        padding: 5px 10px;
        font-size: 13px;
        font-family: $f-light;
    } //.btn--small

    &--transform {
        width: 60px;
        height: 60px;
        line-height: 1;
        border-radius: 50%;
        box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16),
            0 2px 10px 0 rgba(0, 0, 0, 0.12);
        position: fixed;
        bottom: 60px;
        right: 11px;
        z-index: z(fixed);
        @include flexCenter;

        span {
            @include respond-to(xs) {
                display: none;
            }
        }

        i {
            margin-top: -1px;
            font-size: 20px;
            line-height: 1;
            @include respond-min(xs) {
                display: none;
            }
        }
    } //.btn--transform

    &--expanded {
        overflow: initial;

        &::after {
            content: '\f107';
            @include fa('light');
            @include vy;
            right: 9px;
        }

        &[data-position^='top'] {

            .btn {

                &__list {
                    top: -32px;
                }
            }

            &.is-active {

                .btn {

                    &__list {
                        opacity: 1;
                        visibility: visible;
                        top: -92px;
                    }
                }
            }
        }

        &.is-active {

            &::after {
                content: '\f106';
            }

            .btn {

                &__list {
                    opacity: 1;
                    visibility: visible;
                    top: -92px;
                }
            }
        }
    } //.btn--expanded

    &--last {
        margin-left: auto;
    } //.btn--last

    &.is-invalid {
        overflow: initial;

        .btn__error {
            display: block;
        }
    } //.btn.is-invalid

    &.is-loading {

        .btn {

            &__title {
                @include trfY(-150%);
            }
        }

        &::after {
            content: '';
            display: block;
            @include coverdiv;
            background: transparent url('../img/general/loader-3.gif') no-repeat
                center center;
            background-size: contain;
        }
    } //.btn.is-loading

    &.w100 {
        max-width: 250px;
        width: 100%;
    }

    &.w50 {
        max-width: 180px;
        width: 100%;
    }

    &.first {
        margin-right: auto;
    }

    input[type='file'] {
        display: none;
    }

    &-fixed {

        &[data-position] {
            position: fixed;
            z-index: 300;
        }

        &[data-position='bottom right'] {
            right: 45px;
            bottom: 45px;

            @include respond-to(xs) {
                bottom: 60px;
            }
        }

        &[data-position='bottom left'] {
            bottom: 45px;
            left: 11px;

            @include respond-to(xs) {
                bottom: 60px;
            }
        }
    } //.btn-fixed
} //.btn

.btn-floating {
    display: block;
    width: 56px;
    height: 56px;
    padding-top: 10px;
    box-sizing: content-box;
    cursor: pointer;
    transition: bottom 300ms cubic-bezier(0.075, 0.82, 0.165, 1) 1s;
    animation: shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) 3.5s;

    &.fa-enter-active {

        & > .btn-floating__link {

            .icon,
            i {
                transform: rotate(0) translate(-51%, -50%);

                &:not(.icon--first) {
                    visibility: visible;
                    opacity: 1;
                }

                &--first {
                    visibility: hidden;
                    opacity: 0;
                }
            }
        }

        .btn-floating__link {
            pointer-events: auto;

            &[data-btn-tooltip] {

                &::after {
                    right: 100%;
                    visibility: visible;
                    opacity: 1;
                }
            }
        }

        .btn-floating__list {
            visibility: visible;

            .btn-floating__link {
                opacity: 1;
                transform: scale(1);
            }
        }
    } //.btn-floating.fa-enter-active

    &.fa-leave-active {

        .btn-floating__list {

            .btn-floating__link {
                opacity: 0;
                transform: translateY(40px);
            }
        }
    } //.btn-floating.fa-leave-active

    & > .btn-floating__link {
        cursor: pointer;

        .icon,
        i {
            transition: $trs;
            @include centre;
            transform: rotate(90deg) translate(-51%, -50%);
            transform-origin: 0 0;

            &:not(.icon--first) {
                visibility: hidden;
                opacity: 0;
            }
        }
    }

    &__list {
        visibility: hidden;
        position: absolute;
        left: 0;
        right: 0;
        bottom: 66px;
        @include tr;

        a {
            transform: scale(0);
        }
    } //.btn-floating__list

    &__item {
        padding-top: 10px;
        box-sizing: content-box;
    } //.btn-floating__item

    &__link {
        pointer-events: none;
        @include tr();
        border-radius: 50%;
        box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
            0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);

        &[data-btn-tooltip] {

            &::after {
                content: attr(data-btn-tooltip);
                margin-right: 12px;
                padding: 5px 7px;
                color: #fff;
                font-family: $f-medium;
                font-weight: 500;
                font-size: 12px;
                white-space: nowrap;
                border-radius: $border-radius;
                background: #585858;
                opacity: 0;
                visibility: hidden;
                transition: all 0.4s ease-out;
                transition-delay: 0.4s;
                position: absolute;
                bottom: 25%;
                right: 70%;
            }
        }
    } //.btn-floating__link
} //.btn-floating

.buttons-box {
    text-align: center;

    &__text {
        display: block;
        margin-top: 10px;
        text-align: center;
        @extend %textLight;
        font-size: 13px;
        color: #fff;
    } //.buttons-box__text

    &--column {
        @include flexColumn;
        align-items: center;

        .button-animate {
            margin-bottom: 5px;
        }
    } //.buttons-box--column

    .button-animate {
        @include respond-to(xs) {
            width: 80%;
        }

        & + .button-animate {
            margin-left: 30px;

            @include respond-to(xs) {
                margin-left: 0;
                margin-top: 15px;
            }
        }
    }
} //.buttons-box

@keyframes shake {

    10%,
    90% {
        transform: translate3d(-1px, 0, 0);
    }

    20%,
    80% {
        transform: translate3d(2px, 0, 0);
    }

    30%,
    50%,
    70% {
        transform: translate3d(-4px, 0, 0);
    }

    40%,
    60% {
        transform: translate3d(4px, 0, 0);
    }
}
