.bb-select {
    position: relative;
    padding: 0;

    &::before {
        content: '\f107';
        @include fa('light');
        font-size: 18px;
        color: $color-icon;
        @include vy;
        right: 10px;
    }

    @include respond-to(i5) {
        width: 132px;
    }

    select {
        @extend %styleInput;
        padding: 11px 25px 11px 10px;
        width: 100%;
        height: 100%;
        font-family: $f;
        font-size: 16px;
        color: $gray--darkest;
        appearance: none;

        &:focus {
            border-color: $color-border--lighten;
            box-shadow: none;
        }
    } //.bb-select select

    option {
        padding: 0 10px;
    } //.bb-select option
} //.bb-select

.select-color {

    &__ball {
        display: block;
        width: 20px;
        height: 20px;
        border-radius: 50%;

        @each $theme, $color in $text-colors {

            &.#{$theme} {
                background-color: $color;
            }
        }

        & + p {
            padding-left: 9px;
        }
    } //.select-color__ball

    &__line {
        width: 3px;
        height: 90%;
        @include centre('y');
        left: 1px;

        @each $theme, $color in $text-colors {

            &.#{$theme} {
                background-color: $color;
            }
        }
    } //.select-color__line

    &__item {
        width: 100%;
        height: 100%;
        @include flexCenter;
    } //.select-color .custom-select__results--color

    &--palette {
        width: 100px;

        .select2-dropdown {
            width: 163px !important;
            padding: 5px;
        }

        .select2-results {

            &__options {
                display: flex;
                flex-wrap: wrap;
            }

            &__option {
                padding: 5px;
            }

            &__option[aria-selected='true']::after {
                font-weight: 400 !important;
                font-size: 11px !important;
                color: #fff !important;
                right: 9px !important;
            }

            &__option--highlighted[aria-selected] {
                background-color: transparent;
            }
        }
    } //.select-color.palette-mode
} //.select-color
