.form {
    max-width: 375px;
    width: 375px;
    padding: 15px 30px 30px;
    margin: auto;
    overflow: hidden;
    position: relative;

    @include max-screen(375) {
        max-width: 355px;
        width: 355px;
    }

    @include respond-to(i5) {
        max-width: 300px;
        width: 300px;
    }

    &.is-success {

        .form-success {
            @include trfY(0);
            z-index: z(success);
        }

        &--email {

            .form-success--email {
                @include trfY(0);
            }
        }

        &--phone {

            .form-success--phone {
                @include trfY(0);
            }
        }

        .form-success--master,
        .form-success--confirm {
            @include trfY(-200%);
        }

        .fancybox-close-small {
            display: block;
            z-index: z(successBtnClose);
        }
    } //.form.is-success

    &.is-master.is-success {

        .form-success--master {
            @include trfY(0);
        }
    } //.form.is-master.is-success

    &.is-confirm {

        .form-success--confirm {
            @include trfY(0);
            z-index: z(success);
        }

        .fancybox-close-small {
            display: block;
            z-index: z(successBtnClose);
        }
    } //.form.is-confirm

    &.choose-role {

        .form-success--role {
            @include trfY(0);
            z-index: z(success);
        }
    } //.form.choose-role

    &__inner {
        width: 100%;
        @include flexColumn;
        position: relative;
    } //.form__inner

    &__box {
        display: flex;
        margin-bottom: 20px;

        &--role {
            margin-bottom: 12px;

            .bb-checkbox {

                & + .bb-checkbox {
                    margin-left: $gutter;
                }
            }
        } //.form__box--role
    } //.form__box

    &__header {
        margin-bottom: 20px;
        text-align: center;
    } //.form__header

    &__title {
        display: inline-block;
        margin-top: 5px;
        font-size: 20px;
        color: $gray--darkest;
    } //.form__title

    &__subtitle {
        margin-top: 5px;
        @extend %textLight;
        font-size: 14px;
        color: #000;

        span {
            font-family: $f-medium;
        }
    } //.form__subtitle

    &__reg {
        display: flex;
        flex-direction: column;
    } //.form__reg

    &__register {
        margin-top: $gutter;
        text-align: center;

        span {
            font-size: 14px;
        }
    } //.form__register

    &__rememberme {
        display: inline-block;
    } //.form__rememberme

    &__link {
        display: inline-block;
        font-size: 13px;
        font-family: $f-light;
        color: $gray;

        &:hover {
            color: $color-main;
        } //.form__link

        &--recover {
            @extend .form__link;
            margin-left: auto;
        } //.form__link--recover
    } //.form__link

    &__info {
        margin-top: 30px;
        font-size: 13px;
        text-align: center;
        color: $gray--light;

        a {
            display: inline-block;

            &:hover {
                text-decoration: underline;
            }
        }
    } //.form__info

    &__buttons {
        text-align: center;
    } //.form__buttons

    &__btn {
        display: block;
        padding: 9px 0;
        width: 100%;
        margin: 0 auto;
        font-family: $f-medium;
    } //.form__btn

    &__social {
        display: flex;
    } //.form__social

    &__option {
        font-size: 14px;
        text-align: center;
        color: $gray--darken;
        @include flexCenter;
        justify-content: center;

        @include respond-to(xs) {

            .btn-link {
                font-size: 14px;
            }
        }

        span {
            margin-right: 10px;
        }
    } //.form__option

    &__devider {
        margin: 15px auto;
    } //.form__devider

    &__success,
    &__confirm {
        padding: 30px;
        text-align: center;
        background-color: #fff;
        @include coverdiv;
        z-index: z(success);
        @include trfY(-100%);
        transition: all 500ms cubic-bezier(0.645, 0.045, 0.355, 1);
    } //.form__success

    .button-social,
    .btn-social {
        width: 100%;

        & + .button-social {
            margin-left: 10px;
        }
    }

    .form-success__btn {
        display: inline-block;
        margin: 30px auto 0;
    }

    .bb-input {
        width: 100%;
        margin: 0 0 18px;

        &__input {
            width: 100%;
        }
    } //.auth-form .bb-input

    .bb-checkbox {

        &__title {
            font-size: 13px;
            font-family: $f-light;
        }
    } //.auth-form .bb-checkbox
} //.form

.form-success,
.form-confirm {
    padding: 20px;
    text-align: center;
    @include coverdiv;
    background-color: #fff;
    @include flexColumn;
    transform: translateY(-200%);
    transition: all 500ms cubic-bezier(0.645, 0.045, 0.355, 1);
    will-change: transform;
    z-index: z(success);

    &.is-active {
        transform: translateY(0);

        &--blur {
            opacity: 1;
            visibility: visible;

            & ~ * {
                opacity: 0.5;
                @include blur(4px);
            }
        }
    }

    &__inner {
        margin: auto;
    } //.form-success__inner

    &__title {
        display: block;
        margin-bottom: 10px;
        font-size: 20px;
        line-height: 1.5;
        color: $gray--dark;
        font-weight: 300;
    } //.form-success__title

    &__subtitle {
        margin-bottom: 10px;
        font-size: 15px;
        line-height: 1.5;
        color: #000;
    } //.form-success__subtitle

    &__list {
        text-align: left;

        li {
            margin-bottom: 5px;
            padding-left: 15px;
            font-family: $f-medium;
            color: $color-text--darkest;
            font-size: 14px;
            position: relative;

            &::before {
                content: '';
                display: block;
                width: 8px;
                height: 2px;
                background-color: $color-main;
                position: absolute;
                top: 10.5px;
                left: 0;
            }
        }
    } //.form-success__list

    &__info {
        display: block;
        margin-bottom: 50px;
        font-family: $f-light;
        font-weight: 300;
    } //.form-success__info

    &__btn {
        display: block;
        margin-bottom: 15px;
    } //.form-success__btn

    &--blur {
        opacity: 0;
        visibility: hidden;
        transform: translateY(0);
        background-color: transparent;

        & ~ * {
            @include tr(0.5s);
        }

        &.is-active {
            opacity: 1;
            visibility: visible;

            & ~ * {
                opacity: 0.5;
                @include blur(4px);
            }
        }

        .form {

            &__subtitle {
                color: $color-text--darkest;
            }
        }
    } //.form-success--blur

    &--role {
        padding: 20px 50px;

        .form-success {

            &__header {
                margin-bottom: $gutter / 2;
            }
        }

        .bb-checkbox {

            & + .bb-checkbox {
                margin-top: $gutter;
            }

            &__wrap {
                width: fit-content;
                margin: auto;
                @include flexColumn;
                align-items: flex-start;
            }

            &__title {
                font-family: $f-medium;
                font-size: 15px;
                color: $gray;
            }
        }

        .bb-input {
            margin-bottom: 0;
            margin-top: 20px;
        }
    } //.form-success--role

    &--master {
        z-index: z(success);
    } //.form-success--master

    .auth-form__title {
        text-transform: uppercase;
    }
} //.form-success

.success {
    font-family: $f-bold;
    text-transform: uppercase;
    font-size: 18px;
    display: flex;
    align-items: center;
    justify-content: center;
    @include coverdiv;
    z-index: 999;

    @include respond-to(xs) {
        text-align: center;
    }

    span {
        margin: auto;
    }

    &--blur {
        opacity: 0;
        visibility: hidden;

        & ~ * {
            @include tr(0.5s);
        }

        &.is-active {
            opacity: 1;
            visibility: visible;

            & ~ * {
                opacity: 0.5;
                @include blur(4px);
            }
        }
    }
} //.success

.phone-confirm {

    &__text {
        @extend %textLight;
        font-size: 15px;
    } //phone-confirm__text

    &__repeat {
        margin-top: 15px;
    } //phone-confirm__repeat

    &__timer {
        display: block;
        margin-top: 15px;
    } //phone-confirm__timer

    &__input {
        max-width: 90px;
        margin-right: 15px;
        text-align: center;
        font-size: 20px;
        line-height: 1;
        letter-spacing: 1px;
    } //phone-confirm__input

    &__buttons {
        margin-top: 20px;
        text-align: center;
        @include flexColumn;
        justify-content: center;

        .bb-input {
            margin-bottom: 0 !important;
            text-align: left;
            width: 105px;

            &__input {
                margin-right: 0;
            }

            &__error {
                overflow: inherit;
            }
        }
    } //phone-confirm__buttons

    &__box {
        margin-bottom: $gutter;
    } //phone-confirm__box

    &__btn {
        padding: 8px 20px;
    } //phone-confirm__btn
} //phone-confirm
