.page-onepage {

    .header {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        z-index: z(default);

        &__inner {
            border: 0;
        }

        &__logo {
            margin-top: -4px;
        }
    } //.page-onepage .header

    .wrapper {

        &__inner {
            z-index: initial;
        }
    } //.page-onepage .wrapper

    &--home {

        .onepage {
            @include respond-to(sm) {
                background-color: #fff;
            }
        }

        .logo {

            &__link {
                color: #fff;
            }
        }

        .toggle-mnu {

            span {
                background-color: #fff;

                &::before,
                &::after {
                    background-color: #fff;
                }
            }
        }

        .page-title {
            @include respond-to(sm) {
                width: 100%;
                margin: 0;
                font-size: 16px;
                text-align: center;
            }

            &__wrap {
                @include respond-to(sm) {
                    margin-bottom: 0;
                    padding: $gutter 0;
                    background-color: $color-bg--lighten;
                }
            }
        } //.onepage--home .page-title
    } //.onepage--home

    &--about,
    &--reviews {

        .onepage {
            padding-top: 50px;
        }

        @include respond-to(sm) {

            .card-info {

                &__user {
                    @include respond-to(sm) {
                        display: none;
                    }
                }

                &__social {
                    @include respond-to(sm) {
                        display: none;
                    }
                }
            }
        }
    }

    &--about {
        @include respond-to(sm) {

            .page-title {
                text-align: left;

                &__wrap {
                    margin: 0 0 15px;
                    padding: 0;
                    background-color: transparent;
                    align-items: flex-start;
                }
            }
        }
    } //.onepage--about
} //.onepage

.onepage {

    &__gallary {
        margin-bottom: $gutter;

        @include respond-to(sm) {

            .bb-slider {

                &__arrow {
                    display: none !important;
                }
            }

            .slick-slide {
                margin: 0;
            }
        }
    } //.onepage__gallary

    &__social {
        display: none;

        @include respond-to(xs) {
            display: block;
        }
    } //.onepage__social

    .page-title {
        @include respond-to(sm) {
            margin: 0;
            font-size: 16px;
        }
    } //.onepage .page-title

    .user {
        @include flexCenter;

        @include respond-to(sm) {
            margin-bottom: 0;
            padding-bottom: 0;
            border: 0;
        }

        &__avatar {
            margin-right: 15px;
            margin-bottom: 0;
        } //.onepage .user__avatar

        &__img {
            width: 50px;
            height: 50px;
        }

        &__name {
            font-family: $f-medium;
            font-size: 16px;
            line-height: 1.2;
            color: $gray--darkest;
            @include respond-to(xs) {
                font-size: 15px;
            }
        }
    } //.onepage .user

    .card {

        &__content {
            margin-top: 25px;
            background-color: transparent;

            @include respond-to(sm) {
                background-color: $color-bg--lighten;
            }
        }

        &__about {
            @include respond-to(sm) {
                margin-bottom: $gutter;
                padding: 0;
                box-shadow: none;
                background-color: transparent;
            }
        }

        &__about_text {
            padding: 10px 15px;
            @extend %box;
        }

        &-services {
            @include respond-to(sm) {
                margin-bottom: 1px;
            }
        }
    } //.onepage .card

    .card-info {
        top: 0;

        &__top {
            padding: 0;
            border-radius: 0;
            background-color: transparent;
            box-shadow: none;

            @include respond-to(xs) {
                display: none;
            }
        } //.onepage .card-info__top

        &__bg {
            margin: 2px;
            padding: 15px;
            @extend %box;
            @include respond-to(sm) {
                padding: 0;
                margin: 0;
                box-shadow: none;
            }
        } //.onepage .card-info__bg

        &__category {
            margin-bottom: 13px;
            padding-top: $gutter;
            border-top: 1px solid $color-border--lightest;

            @include respond-to(sm) {
                border-top: 0;
            }
        } //.onepage .card-info__category

        &__social {
            width: 100%;
            margin: auto;
        } //.onepage .card-info__social
    } //.onepage .card-info

    .card-adress {
        @include respond-to(sm) {

            &__inner {
                padding: 0;
                box-shadow: $bxs-box;
                background-color: #fff;

                .place-text {

                    &__wrap {
                        padding: 13px 15px 10px;

                        & + .place-text__wrap {
                            border-top: 1px solid $color-border--lightest;
                        }
                    }

                    &__box {
                        margin-bottom: 0;
                    }
                }
            }

            .working__list {
                box-shadow: $bxs-box;
                border: 0;
            }
        }

        &__working,
        &__place {
            @include respond-to(sm) {
                margin-bottom: $gutter;
                padding: 0;
                box-shadow: none;
                background-color: transparent;
            }
        }
    } //.onepage .card-adress__working

    .card-about {
        background-color: transparent;
    } //.onepage .card-about

    .card-request-form {

        &__top {
            margin-top: 0;
        }
    } //.onepage .card-request-form

    .social {

        &__inner {
            @include flexCenter;
            justify-content: center;
        }

        &__devider {
            max-width: 100px;
            margin: $gutter / 1.5 auto;
        }

        .bb-title--devider {
            width: 100%;
            margin: 30px auto;

            @include respond-to(sm) {
                margin: 20px 0;
            }

            &::before,
            &::after {
                width: 38%;
            }
        } //.onepage .social .bb-title--devider
    } //.onepage .social

    .button-social,
    .btn-social {
        padding: 10px;

        @include respond-to(sm) {

            &::before {
                font-size: 20px;
            }

            &-text {
                padding: 10px 0;
            }
        }

        & + .button-social {
            margin-left: $gutter / 2;
        }

        & + .btn-social {
            margin-left: $gutter / 2;
        }
    }

    .bb-accordeon {

        &__item,
        &__title {
            @include respond-to(sm) {
                border-radius: 0;
                box-shadow: none;
            }
        }
    } //.onepage .bb-accordeon
} //.onepage

.footer {
    padding: $gutter 0;
    background-color: $color-bg--light;
    @include flexCenter;
    justify-content: center;

    @include respond-to(sm) {
        padding: $gutter / 2 0;
    }

    &__logo {
        font-size: 14px;
        display: flex;
        align-items: baseline;
    } //.footer__logo

    .logo {
        margin-left: 7px;

        &__link {
            color: $gray--darkest;
        }
    } //.footer .logo
} //.footer

.firstscreen {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    @include sectionOverlay;
    @include cover;

    @include respond-min(md) {
        height: 800px;
    }

    &__inner {
        position: relative;
        display: flex;
        padding: 0 25px;
        color: #fff;
        z-index: 2;
        text-align: center;

        @include respond-min(md) {
            text-align: left;
            max-width: 1200px;
            width: 100%;
        }
    } // .firstscreen__inner

    &__wrapper {
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    &__title {
        color: #fff;
        font-family: $f-medium;
        text-shadow: 0 3px 6px rgba(0, 0, 0, 0.6);
        font-size: 29px;
        letter-spacing: 1px;
        line-height: 1.4;
        text-transform: uppercase;

        @include respond-min(md) {
            width: 100%;
            font-size: 60px;
            line-height: 1.3;
        }
    } // .firstscreen__title

    &__subtitle {
        font-family: $f-light;
        font-size: 18px;
        line-height: 1.2;
        text-shadow: 0 3px 6px rgba(0, 0, 0, 0.6);

        @include respond-min(md) {
            font-size: 20px;
        }
    }// .firstcrenn__subtitle

    &__footnote {
        font-family: $f-light;
        font-size: 14px;
        text-shadow: 0 3px 6px rgba(0, 0, 0, 0.6);

        @include respond-min(md) {
            display: none;
        }
    }// .firstscreen__footnote

    &__btn {
        width: 85%;
        box-shadow: 0 3px 6px rgba(0, 0, 0, 0.15);
        font-size: 17px;

        @include respond-min(md) {
            display: none;
        }

        &:hover {
            box-shadow: inherit;
            transform: none;
        }
    } // .firstscreen__btn

    .mouse-icon {
        border: 2px solid black;
        border-radius: 16px;
        height: 40px;
        width: 30px;
        display: block;
        z-index: 10;
        opacity: 0.7;

        &--white {
            border-color: #fff;

            .wheel {
                background: #fff !important;
            }
        }

        .wheel {
          animation-name: drop;
          animation-duration: 1s;
          animation-timing-function: linear;
          animation-delay: 0s;
          animation-iteration-count: infinite;
          animation-play-state: running;
          position: relative;
          border-radius: 10px;
          background: black;
          width: 2px;
          height: 6px;
          top: 4px;
          margin-left: auto;
          margin-right: auto;
        }

      }

    @-webkit-keyframes drop {

        0% {
          top: 5px;
          opacity: 0;
        }

        30% {
          top: 10px;
          opacity: 1;
        }

        100% {
          top: 25px;
          opacity: 0;
        }

    }

    @keyframes drop {

        0% {
          top: 5px;
          opacity: 0;
        }

        30% {
          top: 10px;
          opacity: 1;
        }

        100% {
          top: 25px;
          opacity: 0;
        }

    }
} //.firstscreen
