.page-promo {

    .main {
        background-color: #fff;
    }

    .footer {
        display: block;
    }

    .social {

        &__inner {
            max-width: max-content;
            margin: $gutter auto;
        }
    }
} //.page-promo

.promo {

    &__title {
        color: $gray--dark;
        font-family: $f-bold;
        font-weight: 600;
        font-size: 20px;
        text-align: center;
    } //.social__title

    &__subtitle {
        display: block;
        color: $gray--dark;
        font-family: $f;
        font-weight: 400;
        text-align: center;
    } //.social__subtitle

    &__suptitle {
        color: $gray;
        font-family: $f-light;
        font-size: 14px;
        text-align: center;
    } //.social__suptitle

    &__list {
        max-width: 425px;
        margin: $gutter auto 0;

        &.list--devider {

            .list__item {
                text-align: center;
                word-wrap: none;
            }

            .list__text {
                margin-left: 0;
                white-space: nowrap;
            }
        }
    } //.promo__list

    &__form {
        margin-bottom: $gutter * 1.5;
        padding: $gutter / 2 0 $gutter / 2;
        border-bottom: 1px solid $color-border--lightest;
        background-color: $color-bg--lighten;

        @include respond-to(i7) {
            max-width: 100%;
            width: 100%;
        }
    } //.promo__form

    &__hero {
        height: calc(100vh - 259px);
        margin-bottom: 0;
        padding-top: $gutter * 2;
        padding-bottom: 0;
        position: relative;
        overflow: hidden;
        @include flexColumn;

        @include respond-to(md) {
            padding: $gutter * 2 10px 0;
        }

        @include respond-to(sm) {
            margin-bottom: 0;
        }

        @include respond-to(xs) {
            height: calc(100vh - 50px);
        }

        @include respond-min(sm) {
            padding-top: $gutter * 4;
        }

        .hero {

            &__subtitle {
                margin-bottom: $gutter * 2;
            }
        }
    } //.promo__hero
} //.promo

.social-btn {
    margin-bottom: $gutter * 1.5;
    padding: $gutter 0;
    background-color: $color-bg--lighten;

    .button-box {
        @include flexColumn;
        align-items: center;
        justify-content: center;

        .btn {
            max-width: 205px;
            min-width: 205px;
            margin: $gutter 0;
            font-family: $f-medium;
        }
    }

    .bb-title--devider {
        max-width: 205px;
        width: 100%;
        margin: auto;
    }
} //.social-btn

.promo-adv {
    padding-bottom: $gutter * 2;
    border-bottom: 1px solid $color-border--lightest;
} //.adv

.adv-image {
    margin-top: auto;
    position: relative;
    bottom: -7.5px;

    &__inner {
        position: relative;
    }

    .slick-slide {
        min-height: 270px;
    }

    &__img {
        width: 16%;
        margin: auto auto 0;
        z-index: 10;
        position: relative;
        bottom: 5px;

        @include respond-to(lg) {
            width: 13%;
        }

        @include respond-to(md) {
            width: 20%;
        }

        @include respond-to(sm) {
            width: 27%;
        }

        @include respond-to(xs) {
            width: 50%;
        }

        @include respond-to(i8) {
            width: 70%;
            bottom: -10px;
        }

        @include respond-to(i7) {
            width: 65%;
            bottom: 5px;
        }

        @include respond-to(i5) {
            width: 53%;
            bottom: 15px;
        }
    }

    &__slide {
        min-height: 270px;
        position: relative;
    }

    .slick-list {
        overflow: inherit;
    }

    .slick-next {
        right: 10px;
    }

    .slick-prev {
        left: 10px;
    }
} //.adv-image

@keyframes sliderFade {

    0% {
        @include tY(200%);
    }

    50% {
        @include tY(0);
    }

    100% {
        @include tY(200%);
    }
}

.users {
    @include cover;
    position: relative;

    &__conut {
        text-align: center;
        font-family: $f-medium;
        font-size: 26px;
        line-height: 1.3;
        color: #fff;
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 80px;
        background-color: rgba(#000, 0.5);
        @include flexColumn;
        justify-content: center;

        span {
            margin: 0 auto;
            font-family: $f-light;
            font-size: 16px;
        }
    }

    .bb-slider {
        margin-top: $gutter * 2;
    }

    .slick-list {
        margin-bottom: $gutter;
    }

    .slick-dots {
        bottom: auto;
        top: -28px;
        z-index: 10;
    }
} //.users
