.banner {

    &__inner {
        height: 50px;
        padding: 0 10px;
        @include flexCenter;
    } //.banner__inner

    &__img {
        width: 35px;
        height: 35px;
        margin-right: $gutter / 2;
        border-radius: 50%;
        border: 2px solid $color-border--darkest;
        overflow: hidden;
        flex-shrink: 0;
    } //.banner__img

    &__title {
        font-family: $f-bold-i;
        font-size: 15px;
        text-transform: uppercase;
    } //.banner__title

    &__btn {
        margin-left: auto;
        padding: 3px 25px;
        font-size: 15px;
        color: #fff;
        border-radius: $border-radius * 8;
        background-color: $color-accent;
        box-shadow: $bxs-box;

        @include respond-to(i7) {
            padding: 3px 13px;
        }
    } //.banner__btn

    &.bg--default {
        color: #fff;

        .banner {

            &__title {
                text-shadow: 2px 2px 4px rgba(#000, 0.7);
            }

            &__img {
                background-color: #ffbef8;
            }
        }
    } //.banner.bg--default
} //.banner
