$breakpoints: (
    i5: 320px,
    i7: 375px,
    i8: 414px,
    xs: 425px,
    sm: 768px,
    md: 1024px,
    lg: 1366px
);

$breakpointsMin: (
    i5: 320px,
    i7: 375px,
    i8: 414px,
    xs: 425px,
    sm: 768px,
    md: 1024px,
    lg: 1366px
);

//z-index
$z-index: (
    bottom: -9999,
    down: -1,
    default: 1,
    fixed: 10,
    success: 50,
    successBtnClose: 51,
    dropdown: 333,
    overlay: 999,
    menu: 1000,
    modal: 1001
);

//FONT
$f: roboto-400, sans-serif;
$f-i: roboto-400i, sans-serif;
$f-c: roboto-400c, sans-serif;
$f-light: roboto-300, sans-serif;
$f-light-i: roboto-300i, sans-serif;
$f-light-c: roboto-300c, sans-serif;
$f-medium: roboto-500, sans-serif;
$f-medium-i: roboto-500i, sans-serif;
$f-medium-c: roboto-500c, sans-serif;
$f-bold: roboto-600, sans-serif;
$f-bold-i: roboto-600i, sans-serif;
$f-bold-c: roboto-600c, sans-serif;

//font for num
$f-num: oswald-400, sans-serif;
$f-num--l: oswald-300, sans-serif;
$f-num--m: oswald-500, sans-serif;
$f-num--b: oswald-600, sans-serif;

$font-size-base: 16px;
$font-family-base: $f;
$line-height-base: 1.5;
$font-weight-base: 400;

//
$base-indent: 20px;
$half-indent: $base-indent / 2;
$section-indent: 50px;

$gutter: $base-indent;

$base-header-size: 60px; //     
$base-header-size--small: 50px; //     

//Base Variebles
$color-accent: #00dfbf;
$color-main: #ff8272;
$color-third: #fff06b;

$color-muted: #d6d6d6;
$color-primary: #ff8272;
$color-primary--light: #ff9280;
$color-success: #8cc152;
$color-success--light: #a0d468;
$color-info: #2b72bd;
$color-info--light: #1d8acb;
$color-danger: #f33155;
$color-danger--light: #d9534f;
$color-warning: #ffc107;

//gray
$gray--darkest: #242626;
$gray--darken: #353737;
$gray--dark: #464646;
$gray: #585a59;
$gray--light: #9b9b9b;
$gray--lighten: #e0e0e0;
$gray--lightest: #f9f9f9;

//text
$color-text--darkest: $gray--darkest;
$color-text: $gray;
$color-text--light: $gray--light;
$color-text--lighten: $gray--lighten;

//btn
$color-btn--darken: #3e3737;
$color-btn--dark: #6b6464;

//bg
$color-bg--accent: #fff4f0;
$color-bg--darken: #191919;
$color-bg--dark: #222;
$color-bg: #2f2f2f;
$color-bg--light: #eee;
$color-bg--lighten: #f5f5f5;
$color-bg--lightest: #f7f7f8;

//Icon
$color-icon-darken: $gray;
$color-icon-dark: #727272;
$color-icon: #a5a5a5;
$color-icon--light: #cacaca;

//border
$color-border--darkest: $gray--darkest;
$color-border--darken: $gray--darken;
$color-border--dark: $gray;
$color-border: #a5a5a5;
$color-border--light: #c2c2c2;
$color-border--lighten: #dedede;
$color-border--lightest: #efefef;

$border-radius: 3px;

$color-placeholder: #999;

//
$bxs: 1px 1px 10px #ddd;
$bxs-hover: 1px 1px 10px rgba($color-main, 0.6);
$bxs-box: 0 2px 4px 0 rgba(0, 0, 0, 0.1), 0 0 4px 0 rgba(0, 0, 0, 0.1);

//Transition
$trsMenu: all 0.3s cubic-bezier(0.25, 0.8, 0.4, 0.95);
$trs: all 0.4s ease-in-out;

//button colors
$button-colors: (
    accent: $color-accent,
    default: $color-main,
    success: $color-success,
    error: $color-danger,
    error-light: $color-danger--light,
    warning: $color-warning,
    info: $color-info,
    muted: $color-muted,
    gray: $gray--dark,
    gray-light: #808080,
    secondary: #a5a5a5,
    white: #fff
);

$text-colors: (
    red: red,
    pink: #fe0061,
    purple: #ab00b6,
    blue: #3c4ebc,
    blue--light: #0096fb,
    blue--lightest: #00bfd8,
    green--dark: #009988,
    green: #00b341,
    green--light: #c9df00,
    yellow: #ffec00,
    orange: #ff9300,
    accent: $color-main
);

$btn-social-color: (
    whatsapp: #23d366,
    viber: #7d3daf,
    vk: #4a76a8
);

//Icons
$icon-favorites: '\f004'; //
$icon-info: '\f0f3'; //
$icon-search: '\f002'; //
$icon-link: '\f0c1'; //

$icon-enter: '\f090'; //
$icon-home: '\f015'; //dashbord
$icon-lk: '\f007'; // 
$icon-request: '\f4b6'; //
$icon-profile: '\f015'; //
$icon-masters: '\f2a7'; //
$icon-admins: '\f0c0'; //
$icon-works: '\f030'; //
$icon-services: '\f0b1'; //
$icon-stock: '\f295'; //
$icon-balance: '\f0d6'; //
$icon-statistica: '\f201'; //
$icon-options: '\f013'; //
$icon-location: '\f3c5'; //
$icon-vacancy: '\f2bb'; //
$icon-traning: '\f501'; //

$icon-whatsapp: '\f232'; //whatsapp
$icon-viber: '\f409'; //viber
$icon-vk: '\f189'; //vk

$btn-icon: (
    fav: $icon-favorites,
    info: $icon-info,
    search: $icon-search,
    link: $icon-link
);

$btn-social-icon: (
    whatsapp: $icon-whatsapp,
    viber: $icon-viber,
    vk: $icon-vk
);

$mobile-nav-icons: (
    enter: $icon-enter,
    home: $icon-home,
    lk: $icon-lk,
    request: $icon-request,
    fav: $icon-favorites,
    link: $icon-link
);

//Lk navigation icons
$lk-nav-icons: (
    profile: $icon-profile,
    masters: $icon-masters,
    admins: $icon-admins,
    works: $icon-works,
    services: $icon-services,
    request: $icon-request,
    stock: $icon-stock,
    balance: $icon-balance,
    statistica: $icon-statistica,
    options: $icon-options,
    fav: $icon-favorites,
    location: $icon-location,
    vacancy: $icon-vacancy,
    traning: $icon-traning
);

//Modal Variebles
$modal-border-width: 1px;
$modal-border-color: #e8e8e8;
$modal-border-radius: $border-radius;

$modal-box-shadow: 0 0.25rem 0.5rem rgba(#000, 0.5) !default;

$modal-backdrop-bg: #000 !default;
$modal-content-bg: #fff !default;
$modal-backdrop-opacity: 0.4 !default;
$modal-header-padding: 15px !default;
$modal-inner-padding: 15px !default;

$modal-md: 500px !default;
$modal-sm: 300px !default;
$modal-lg: 800px !default;

$transition-collapse: height 0.35s ease !default;
$transition-fade: opacity 0.15s linear !default;
