$trsM: all 0.4s ease-in-out;
$trsD: 0.4s;

.nav {
    @include respond-to(sm) {
        display: none;
        position: absolute;
        left: 0;
        top: 50px;
        width: 100%;
        background-color: #fff;
        box-shadow: 1px 5px 10px rgba($gray, 0.4);
        z-index: 200;
    }

    &__list {
        width: 100%;
        text-align: center;
        @include flexCenter;
        justify-content: center;
        @include respond-to(sm) {
            display: flex;
            flex-direction: column;
            width: 100%;
            margin: 0;
            padding: 15px 0;
            text-align: left;
        }
    } //.nav__list

    &__item {
        display: inline-block;
        position: relative;
        @include respond-to(sm) {
            width: 100%;
            margin-right: 0;
            border-top: 1px solid $color-border--lightest;
        }

        &:first-child {
            margin-left: 0;
        }

        &:last-child {
            margin-right: 0;
        }

        &:hover {

            .nav__link {
                color: #000;
                background-color: #f1f1f1;
                @include respond-to(sm) {
                    color: $color-main;
                }

                &::after {
                    width: 100%;
                }
            }

            .nav-dropdown {
                display: block;
                pointer-events: all;
            }
        }

        &.is-active {

            .nav__link {
                color: #000;
                @include respond-to(sm) {
                    color: $color-main;
                }

                &::after {
                    width: 100%;
                }
            }
        }
    } //.nav__item

    &__link {
        display: block;
        padding: 0 20px;
        font-family: $f-light;
        font-size: 13px;
        line-height: 40px;
        text-transform: uppercase;
        letter-spacing: 1px;
        color: $gray--dark;
        position: relative;
        transition: initial;
        @include respond-to(sm) {
            padding: 0 15px;
            width: 100%;
            margin: auto;
            text-align: center;

            &::after {
                display: none;
            }
        }
    } //.nav__link

    &--blog {
        margin-left: auto;

        .nav {

            &__item {
                text-align: center;

                & + .nav__item {
                    margin-left: 30px;
                    @include respond-to(sm) {
                        margin-left: 0;
                        margin-top: 10px;
                    }
                }

                &:hover,
                &.is-active {

                    .nav__link {
                        background-color: transparent;
                        background-image: linear-gradient(
                            180deg,
                            $color-third,
                            $color-third
                        );
                        background-repeat: no-repeat;
                        background-position: 4px bottom;
                        background-size: 100% 10px;
                    }
                }

                &:last-child {

                    .nav__link {
                        padding-right: 0;
                    }
                }
            }

            &__link {
                display: inline-block;
                width: auto;
                padding: 0;
                line-height: 1.5;
            }
        }
    } //.nav--blog
} //.nav

.menu {
    width: $base-header-size;
    background-color: $color-bg;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    z-index: z(dropdown);
    transition: width ease-out 0.3s,
        left 0.3s cubic-bezier(0.25, 0.8, 0.4, 0.95);

    @include respond-to(xs) {
        width: 100%;
        top: auto;
        bottom: 0;
        border-top: 1px solid $color-border--lightest;

        [tooltip] {

            &::before,
            &::after {
                display: none;
            }
        }
    }

    &.is-open {
        width: 248px;

        .menu {

            &__hamburger {
                left: 188px;
            }

            &__logo {
                opacity: 1;
                visibility: visible;
                @include tX(0);
            }

            &__text {
                opacity: 1;
                visibility: visible;
                @include tX(0);
            }

            &__item {

                &.dropdown-active {

                    .menu__dropdown {
                        @include trfX(190px);
                    }
                }
            }
        }

        [tooltip] {

            &::before,
            &::after {
                display: none;
            }
        }
    } //.menu.is-open

    &__logo {
        margin: auto 0 auto 23px;
        opacity: 0;
        visibility: hidden;
        @include tX(-1000%);
        transition: transform 0.35s cubic-bezier(0.22, 0.22, 0.02, 1.07);
        transition-delay: 0.1s;

        .logo__link {
            color: #fff;
        }
    } //.menu__logo

    &__hamburger {
        display: inline-flex;
        cursor: pointer;
        border-bottom: 1px solid $color-bg--dark;
        position: absolute;
        top: 0;
        left: 0;
        @extend %xs50;
        transition: left ease-out 0.3s;

        @include respond-to(xs) {
            display: none;
        }
    } //.menu__hamburger

    &__top {
        width: 100%;
        height: $base-header-size + 1;
        @extend %xs50;
        border-bottom: 1px solid $color-bg--dark;
        position: absolute;
        top: 0;
        left: 0;
        @include flexCenter;

        @include respond-to(xs) {
            display: none;
        }
    } //.menu__top

    &__list {
        height: calc(100% - #{$base-header-size});
        background-color: $color-bg;
        position: relative;
        top: $base-header-size + 1;
        @include flexColumn;

        @include respond-to(xs) {
            height: $base-header-size--small;
            top: 0;
            flex-direction: row;
            justify-content: space-between;
        }
    } //.menu__list

    &__item {
        @for $i from 1 through 10 {

            &:nth-child(#{$i}) {

                .menu__text {
                    transition-delay: calc(#{$i}s / 10);
                }
            }
        }

        @include respond-to(xs) {
            margin: auto;
        }

        &.is-active {

            .menu {

                &__link {
                    @include min-screen(426) {
                        background-color: $color-bg--dark;
                    }
                }

                &__icon {
                    color: #fff;
                    @include min-screen(426) {
                        background-color: $color-bg--dark;
                    }
                    @include respond-to(xs) {
                        color: $color-main;
                    }
                }
            }
        } //.menu__item.is-active

        &.dropdown-active {

            .menu {

                &__link {
                    background-color: $color-bg--dark;
                }

                &__dropdown {
                    @include trfX(0);

                    @include respond-to(xs) {
                        @include trfY(0);
                    }
                }
            }
        } //.menu__item.dropdown-active

        &--last {
            margin-top: auto;

            .menu__link {
                border-bottom: none;
                border-top: 1px solid $color-bg--dark;

                @include respond-to(xs) {
                    border-top: none;
                }
            }
        } //.menu__item--last

        &--more {
            @include min-screen(481) {
                display: none;
            }

            .menu__link {
                color: #fff;
            }
        } //.menu__item--more
    } //.menu__item

    &__link {
        width: 100%;
        height: $base-header-size;
        padding-left: $base-header-size;
        position: relative;
        @include flexCenter;
        justify-content: center;
        @extend %xs50;

        &:hover {
            background-color: $color-bg--dark;

            .menu {

                &__icon {
                    color: #fff;
                    @include min-screen(426) {
                        background-color: $color-bg--dark;
                    }

                    &--data {

                        &::after {
                            animation: none;
                        }
                    }
                }

                &__text {
                    color: #fff;
                }
            }
        }

        @include respond-to(xs) {
            padding-left: 0;
            border-bottom: none;
        }
    } //.menu__link

    &__text {
        width: 100%;
        font-family: $f-light;
        font-size: 15px;
        color: $gray--light;
        white-space: nowrap;
        opacity: 0;
        visibility: hidden;
        transition: transform 0.4s cubic-bezier(0.22, 0.22, 0.02, 1.07),
            opacity 0.35s ease;
        @include tX(-100%);
        @include flexCenter;

        @include respond-to(xs) {
            display: none;
        }
    } //.menu__text

    &__icon {
        width: $base-header-size;
        height: $base-header-size;
        font-size: 20px;
        color: $color-icon;
        @include tr;
        position: absolute;
        left: 0;
        z-index: z(default);
        @include flexCenter;
        justify-content: center;
        @extend %xs50;

        &::after {
            content: '';
            display: none;
            height: 100%;
            width: 2px;
            background-color: $color-main;
            position: absolute;
            top: 0;
            left: 0;
            bottom: 0;
        }

        @include min-screen(426) {
            background-color: $color-bg;
        }
    } //.menu__icon

    &__count {
        @extend %badge;
        font-style: normal;
        background-color: $color-main;
        border: 2px solid $color-bg;
        position: absolute;
        top: 6px;
        left: 23px;
        animation: pulse 1.5s ease-out infinite;
        z-index: z(default);
        @include respond-to(xs) {
            animation: none;
        }
    } //.menu__count

    &__overlay {
        position: fixed;
        @extend .overlay;

        &.is-active {
            display: block;
        }
    } //.menu__overlay

    &.bg--white {
        background-color: #fff;

        .menu {

            &__list {
                @include border(right);
                background-color: #fff;

                @include respond-to(xs) {
                    border-right: 0;
                }
            }

            &__link,
            &__icon {
                color: #000;
                background-color: #fff;
            }

            &__link {

                &:hover {
                    background-color: #fff;

                    .menu__icon {
                        color: #000;
                        background-color: #fff;
                    }
                }
            }

            &__item {
                border-bottom: 0;

                &.is-active {
                    background-color: #fff;

                    .menu {

                        &__icon {
                            color: #000;
                            background-color: #fff;
                        }
                    }
                }

                &.dropdown-active {

                    .menu__link {
                        background-color: transparent;
                    }
                }

                &--last {

                    .menu__link {
                        border-top: 0;
                    }
                }
            }

            &__count {
                font-family: $f-num;
                color: #fff;
                border-color: #fff;
            }
        }
    } //.menu.bg--white
} //.menu

.mobile-nav {
    background-color: $color-bg--lightest;
    position: fixed;
    width: 300px;
    height: 100%;
    left: 0;
    top: 0;
    z-index: z(menu);
    @include trfX(-100%);
    transition: all 0.4s ease-in-out 0s;
    overflow-x: hidden;
    @include scroll;
    @include flexColumn;

    @include respond-to(i5) {
        width: 272px;
    }

    &__inner {
        padding: 10px;
    } //.mobile-nav__inner

    &__header {
        // height: 50px;
        min-height: 150px;
        padding: 0 10px;
        position: relative;
        overflow: hidden;
        @include cover;
        @include flexCenter;

        .header-user {
            position: absolute;
            left: 10px;
            bottom: 0;

            &__name,
            &__link {
                color: #fff;
            }
        }

        .button-box {
            position: absolute;
            top: 5px;
            right: 10px;

            i {
                font-size: 18px;
                color: #fff;
            }
        }
    } //.mobile-nav__header

    &__title {
        display: block;
        margin-bottom: 10px;
        padding-left: 20px;
        text-transform: uppercase;
        font-family: $f-bold;
        font-size: 11px;
        color: $color-text;
        letter-spacing: 1px;
    } //.mobile-nav__title

    &__list {
        margin-bottom: 10px;
        background-color: #fff;
    } //.mobile-nav__list

    &__item {
        border-left: 4px solid transparent;

        &:last-child {
            border-bottom: 1px solid $color-border--lightest;
        }

        &.is-active {
            border-left: 4px solid $color-main;

            &:last-child {
                border-bottom-color: transparent;
            }
        }

        .counter {
            @include vy;
            right: 15px;
        } //.mobile-nav__item .counter

        &.bb-accordeon__item {
            border-bottom: 0;

            .bb-accordeon {

                &__content {

                    a {
                        display: block;
                        margin-left: 35px;
                        padding: 5px 0 4px;
                        font-size: 15px;
                        color: $color-text;
                    }
                }
            }
        }
    } //.mobile-nav__item

    &__link {
        display: block;
        font-size: 17px;
        color: $color-text;
        position: relative;

        &::before {
            @include fa('light');
            font-size: 18px;
            @include vy;
            left: 13px;
        }

        @each $theme, $icon in $mobile-nav-icons {

            &--#{$theme} {

                &::before {
                    content: $icon;
                }
            }
        }

        &--lk {

            &::before {
                left: 14px;
            }
        } //.mobile-nav__link--lk

        &--enter {
            margin-left: 9px;
            padding-left: 30px;
            color: #fff;

            &::before {
                line-height: 1;
                left: 0;
                top: 3px;
                transform: translateY(0);
            }
        } //.mobile-nav__link--enter

        &--leave {
            min-height: 43px;
            margin-top: auto;
            padding: 10px 10px 10px 17px;
            font-size: 15px;
            color: $color-text;
            background-color: #fff;
            border-top: 1px solid $color-border--lightest;
            @include flexCenter;

            &:hover {
                text-decoration: none;
            }

            i {
                margin-right: 10px;
                font-size: 18px;
            }
        } //.mobile-nav__link--leave

        &.bb-accordeon__title {
            padding: 0;
        }
    } //.mobile-nav__link

    &__text {
        display: block;
        padding: 7px 0;
        margin-left: 45px;
        border-top: 1px solid $color-border--lightest;
    } //.mobile-nav__text

    &__icon {
        font-size: 18px;
        @include vy;
        left: 13px;

        &.fa-address-book {
            left: 14px;
        }

        &.fa-users,
        &.fa-box-full {
            left: 11px;
        }
    } //.mobile-nav__icon

    &__services {
        margin-top: 13px;
        margin-bottom: $gutter;
        padding-top: 15px;
        background-color: #fff;
        border-top: 1px solid $color-border--lightest;

        a {
            display: block;
            padding: 3px 0;
            @extend %textLight;
            color: $gray--light;
        }

        .bb-accordeon {

            &__item {
                margin-left: 20px;

                &:last-child {
                    margin-left: 0;
                    padding-left: 20px;
                }
            }

            &__title {
                padding: 10px 0 9px;
                font-size: 15px;
                color: $color-text;
            }
        }
    } //.mobile-nav__services

    &__btn {
        display: inline-block;
        margin-left: 20px;
        margin-top: 5px;
        padding: 3px 10px 4px;
        letter-spacing: 0;

        @include respond-to(xs) {
            margin-left: 9px;
            font-size: 15px;
        }

        @include respond-to(i5) {
            margin-left: 10px;
        }
    } //.mobile-nav__btn

    &.bg--dark {
        background-color: $gray--darken;

        .mobile-nav {

            &__title {
                color: rgba(#fff, 0.3);
            }

            &__item {

                &:last-child {
                    border-color: $color-border--dark;
                }

                &.is-active {
                    border-color: $color-main;
                    background-color: #2c2d2d;
                }
            }

            &__link {
                @extend %textLight;
                color: #fff;

                &--leave {
                    color: #fff;
                    border-color: $color-border--dark;
                }
            }

            &__text {
                border-color: $color-border--dark;
            }

            &__services {

                &__title {
                    font-family: $f-light;
                }

                a {
                    color: $gray--light;
                }
            }
        }

        .header-user {

            &__name {
                color: $gray--lighten;
            }

            &__logout {
                color: $gray--light;
            }
        }

        .counter {
            font-family: $f-num;
            color: #fff;
        }
    } //.mobile-nav.bg--dark

    .header-user {
        height: 50px;
        padding: 0;
        transition: all 0.6s ease-in-out;
        transition-delay: $trsD;
        display: flex;

        &.is-active,
        &:hover {
            background-color: transparent;
        }

        &.is-authorized {

            .header-user__img,
            .header-user__inner,
            .header-user__logout {
                display: block;
            }

            .mobile-nav__link--enter {
                display: none;
            }
        }

        &::after {
            display: none;
        }

        &__link {
            width: 100%;
            padding-left: 0;
            @include flexCenter;
            flex-wrap: wrap;
        }

        &__img {
            display: none;
            margin-left: 0;
            margin-right: 5px;
        }

        &__inner {
            display: none;
        }

        &__name {
            display: block;
            line-height: 1;
        }

        &__desc {
            flex: 1;
            line-height: 1;
        }

        &__logout {
            display: none;
            margin-left: auto;
            color: $color-text;
        }
    } //.mobile-nav .header-user
} //.mobile-nav

.nav-dropdown {
    display: none;
    pointer-events: none;
    background: rgba(255, 255, 255, 0.95);
    border-bottom: 4px solid $color-main;
    box-shadow: 3px 10px 20px rgba(0, 0, 0, 0.2);
    min-width: 300px;
    position: absolute;
    top: 40px;
    z-index: z(dropdown);

    &--left {
        left: 0;
    } //.nav-dropdown--left

    &--right {
        right: 0;
    } //.nav-dropdown--right

    &__list {
        padding: 10px 35px;
        text-align: left;
        box-sizing: border-box;
    } //.nav-dropdown__list

    &__item {
        padding: 6px 0;
    } //.nav-dropdown__item

    &__link {
        display: inline-block;
        text-align: left;
        font-size: 13px;
        color: #000;
        text-decoration: none;
        @include listIcon;

        &::before {
            top: 9px;
        }

        &:hover {
            text-decoration: underline;
            color: #000;
        }
    } //.nav-dropdown__link
} //.nav-dropdown

.menu-dropdown {
    padding: 30px 15px;
    background-color: #fff;
    border-right: 1px solid #dedede;
    border-top: 1px solid #dedede;
    position: absolute;
    left: $base-header-size;
    top: 0;
    height: 100%;
    width: 188px;
    z-index: -1;
    @include trfX(-100%);
    transition: transform ease-out 0.3s;
    will-change: transform;

    @include responseWidth;

    @include respond-to(xs) {
        padding: 0;
        border: none;
        background-color: transparent;
        border-radius: $border-radius * 2;
        box-shadow: 0;
        position: fixed;
        left: 10px;
        right: 10px;
        top: auto;
        bottom: 10px;
        height: auto;
        z-index: z(modal);
        @include trfX(0);
        @include trfY(100vh);
        transition: $trsM;
    }

    &__title {
        display: block;
        padding: 0 15px;
        margin-bottom: 15px;
        color: $gray--light;
        font-size: 13px;
        letter-spacing: 1px;
        text-transform: uppercase;

        @include respond-to(xs) {
            margin-bottom: 0;
            padding: 15px;
            text-align: center;
            background-color: #fff;
            border-radius: 12px 12px 0 0;
        }
    } //.menu-dropdown__title

    &__list {
        @include respond-to(xs) {
            border-radius: 0 0 $border-radius $border-radius;
            overflow: hidden;
        }
    } //.menu-dropdown__list

    &__item {
        margin: 1px auto;

        @include respond-to(xs) {
            width: 100%;
            background-color: #fff;

            &:first-child {
                border-top: 1px solid $color-border--lightest;
            }

            &:last-child {

                &:not(.menu-dropdown__item--abort) {
                    border-bottom: none;
                    border-radius: 0 0 8px 8px;
                }
            }

            &:not(.menu-dropdown__item--abort) {
                margin: 0 auto;
                border-bottom: 1px solid $color-border--lightest;
            }
        }

        &--abort {
            margin-top: 8px;
            border: none;
            border-radius: $border-radius * 4;

            @include min-screen(481) {
                display: none;
            }
        } //.menu-dropdown__item--abort
    } //.menu-dropdown__item

    &__link {
        display: block;
        padding: 7px 15px;
        color: $gray;
        font-size: 15px;
        border-radius: $border-radius;

        &:hover {
            color: $color-main;
            background-color: lighten($color-main, 25%);
        }

        @include respond-to(xs) {
            padding: 13px 0;
            text-align: center;
            font-size: 18px;
            color: $color-info;

            &:hover {
                color: $gray;
                background-color: transparent;
            }
        }
    } //.menu-dropdown__link
} //.menu-dropdown

.toggle-mnu {
    display: none;
    width: 50px;
    height: 50px;
    padding: 11px;
    cursor: pointer;

    @include respond-to(sm) {
        display: block;
    }

    span {
        position: relative;
        display: block;
        width: 100%;
        height: 2px;
        margin-top: 4px;
        background-color: $gray--darkest;
        transition: all 0.3s;
        backface-visibility: hidden;
        border-radius: $border-radius;

        &::after,
        &::before {
            content: '';
            height: 2px;
            width: 100%;
            background-color: $gray--darkest;
            border-radius: $border-radius;
            backface-visibility: hidden;
            transition: all 0.3s;
            position: absolute;
            left: 0;
            top: 9px;
        }

        &::after {
            top: 18px;
        }
    }

    &.on {

        span {
            background-color: transparent;

            &::before {
                transform: rotate(45deg) translate(-1px, 0px);
            }

            &::after {
                transform: rotate(-45deg) translate(6px, -7px);
            }
        }
    }
} //.toggle-mnu

.hamburger {
    width: $base-header-size;
    height: $base-header-size;
    transition-duration: 0.5s;
    display: block;
    position: relative;

    span {
        position: relative;
        display: block;
        width: 27px;
        height: 2px;
        margin: 29px auto 0;
        background-color: $color-icon;
        border-radius: 15px;
        transition: all 0.3s;
        backface-visibility: hidden;

        &:hover {
            cursor: pointer;
        }

        @include respond-to(xs) {
            background-color: $gray--light;
        }

        &::after,
        &::before {
            content: ' ';
            width: 100%;
            height: 2px;
            background-color: $color-icon;
            backface-visibility: hidden;
            border-radius: 15px;
            position: absolute;
            left: 0;
            top: 9px;
            transition: all 0.3s;

            @include respond-to(xs) {
                background-color: $gray--light;
            }
        }

        &::before {
            top: -9px;
        }

        &::after {
            top: 9px;
        }
    } //.hamburger span

    @include respond-min(xs) {

        &.on {

            span {
                transition: 0.5s;

                &::after,
                &::before {
                    width: 16px;
                }

                &::before {
                    transform: translate(-3px, 4px) rotate(-45deg);
                }

                &::after {
                    transform: translate(-3px, -4px) rotate(45deg);
                }
            }
        }
    }
} //.hamburger
