.screen {
    padding: 45px 0;
    background-color: #fff;

    @include respond-min(md) {
        padding: 60px 0;
    }

    &__title {
        position     : relative;
        color        : #000;
        font-family  : $f;
        font-size    : 25px;
        line-height  : 1.3;
        margin-bottom: 35px;

        @include respond-min(md) {
            margin-bottom: 60px;
            line-height  : 1.5;
            font-size    : 35px;
            text-align   : center;
        }

        &.align-center {
            text-align: center;
        }

        &--white {
            color: #fff;
        } //.screen__title--white
    } //.screen__title

    &__description {
        font-family: $f;
        font-size  : 18px;
        line-height: 1.7;
        color      : $color-text;
    } //.screen__description

    &__icon-slider {
        position: relative;
    } //.screen__icon-slider

    &__btn {
        display: block;
        width: 100%;
        font-size: 17px;

        @include respond-min(md) {
            width: 300px;
            margin: auto;
        }
    } //.screen__btn

    &.bg-grey {
        background: $color-bg--lightest;
    } //.screen.grey
} //screen

.text-block {
    display       : flex;
    flex-direction: column;
    align-items   : center;

    &__wrap {
        @include row-flex;
        margin-bottom: 40px;

        .text-block {
            @include col;
            @include size(4);
            @include size-sm(6);
            @include size-xs(12);
        }
    } //text-block__wrap

    &__icon {
        width        : 119px;
        height       : 119px;
        margin-bottom: 20px;
    } //text-block__icon

    &__title {
        margin-bottom: 20px;
        font-size    : 20px;

        @include respond-min(md) {
            font-size: 25px;
        }
    } //text-block__title

    &__sub {
        font-family: $f-light;
        font-size  : 16px;
        line-height: 1.5;
        text-align : center;

        @include respond-min(md) {
            font-size: 17px;
        }
    } //text-block__sub

    &--icon {

        @include respond-min(md) {
            margin-bottom: 50px;
        }

        & + .text-block--icon {
            margin-top: 50px;

            @include respond-min(md) {
                margin-bottom: 50px;
                margin-top: 0;
            }
        }
    } //text-block--icon
} //text-block

.icon-list {
    position   : relative;
    display    : flex;
    align-items: center;

    & + .icon-list {
        margin-top: 20px;

        @include respond-min(md) {
            margin-top: 0;
        }
    }

    @include respond-min(md) {
        flex-direction: column;
        align-items   : center;
        width         : 30%;
    }

    &__wrap {
        @include row-flex;
        margin-bottom: 40px;
        margin-left: 0;

        @include respond-min(md) {
            display: flex;
            justify-content: center;
            margin-bottom: 90px;
        }

        .text-block {
            @include col;
            @include size(4);
            @include size-sm(6);
            @include size-xs(12);
        }
    } //.icon-list__wrap

    &__icon {
        width       : 40px;
        margin-right: 20px;

        @include respond-min(md) {
            display        : flex;
            justify-content: center;
            width          : 100px;
            margin-bottom  : 20px;
            margin-right   : 0;
        }
    } //.icon-list__icon

    &__title {
        font-family: $f-medium;
        font-size  : 20px;

        @include respond-min(md) {
            text-align: center;
            font-size : 35px;
        }
    } //.icon-list__title

    &__sub {
        font-family: $f-light;
        font-size  : 16px;
    } //.icon-list__sub

    &--white {
        color: #fff;
    } //.icon-list--white
} //.icon-list

.review {

    &__img {
        width: 50px;
        margin-top: -7px;
        margin-left: 15px;
        float: right;
    }

    &__sub {
        margin-bottom: 55px;
        font-family  : $f-light;
        font-size    : 16px;
        color        : $color-text--light;
        line-height  : 1.7;
    }// review__sub

    &__author {
        line-height: 0.8;
        text-align : right;
        font-size  : 14px;
        color      : #6b6b6b;

        p {
            font-family: $f-medium;
            color      : $color-text--darkest;
        }
    }// review__author
} //review

.form {
    width: 100%;

    @include respond-min(md) {
        color: $color-text--darkest;
        background-color: rgba(255,255,255, 0.75);
        border-radius: $border-radius;
        margin: 0;
        margin-left: auto;
    }

    &__title {
        font-family: $f-bold;
        font-size: 27px;
        line-height: 1.2;
        width: 260px;
    }

    .bb-checkbox__box {
        border-color: $color-border;
    }

    .bb-checkbox--radio
    .bb-checkbox__title {
        font-size: 14px;
    }

    .bb-checkbox:hover
    .bb-checkbox__box {
        border-color: $color-main;
    }

    .bb-title--devider::after,
    .bb-title--devider::before {
        border-top: 1px solid $gray--light;
    }
} //.form

.beauty-box {
    color: $color-text--darkest;
    font-family: encode-700, sans-serif;

    span {
        color: $color-main;
    } //.beauty-box span

    &--white {
        color: #fff;
    } //.beauty-box--white
} //.beauty-box

.video {
    position: relative;
    padding-bottom: 56.25%;

    @include respond-min(md) {
        margin-left: 0;
    }

    iframe {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
} //.screen__video
