.select2 {

    &__icon,
    .fal {
        margin-right: 5px;
    }
}

.select2-container {
    min-width: 105px;
    width: 100% !important;
}

.select2-dropdown {
    border-radius: $border-radius;
    z-index: 150;

    .select2-search__field {
        @extend %textLight;
        font-size: 14px;
    }
}

.select2-container--default .select2-results>.select2-results__options {
    max-height: 193px;
}

.select2-container--default .select2-selection--single {
    height: 40px;
    line-height: 40px;
    border-radius: $border-radius;
    border-color: $color-border--lightest;
    outline: none;
}

.select2-container--default .select2-selection__placeholder {
    font-family: $f-light-i;
    font-size: 14px;
}

//.select2-container--default .select2-selection__placeholder

.select2-container--default .select2-selection--single .select2-selection__rendered {
    line-height: 39px;
}

.select2-container--default .select2-selection--single .select2-selection__arrow {
    height: auto;
    width: auto;
    right: 10px;
    top: 0;
}

.select2-container--default .select2-selection--single .select2-selection__arrow b {
    border: none;
    margin-left: auto;
    margin-top: auto;
    left: auto;
    position: absolute;
    top: 0;
    right: 8px;

    &::before {
        content: '\f107';
        @include fa('light');
        font-size: 17px;
        color: $gray;
    }

    //.select2-container--default .select2-selection--single .select2-selection__arrow b::before
}

//.select2-container--default .select2-selection--single .select2-selection__arrow b

.select2-container--default {

    &.select2-container--open {

        .select2-selection--single,
        .select2-selection--multiple {
            border-color: $color-border--lighten;
            box-shadow: $bxs-box;

            .select2-selection__arrow {

                b {

                    &::before {
                        content: '\f106';
                    }
                }
            }
        }
    }
}

.select2-container--open .select2-dropdown--below {
    box-shadow: $bxs-box;
    border-color: $color-border--lighten;
    z-index: 1001;
}

.select2-results__option {
    height: 32px;
    line-height: 20px;
    padding-left: 10px;
    font-family: $f;
    font-size: 14px;
    @include text-overflow;
    position: relative;

    &[aria-disabled='true'] {
        color: $color-text !important;

        // &::before {
        //     content: '';
        //     display: block;
        //     width: 3px;
        //     height: 100%;
        //     background-color: $color-main;
        //     position: absolute;
        //     left: 0;
        //     top: 0;
        // }
    }

    //.select2-results__option[aria-disabled=true]

    .metro-icon {

        &::before {
            margin-right: 9px;
        }
    }
}

//.select2-results__option

.select2-container--default .select2-results>.select2-results__options {
    @include bb-scroll--thin;
}

.select2-container--default .select2-results__option[aria-disabled='true'] {
    color: $gray--light;
}

.select2-dropdown,
.select2-container--default .select2-search--dropdown .select2-search__field {
    border-color: $color-border--lighten;
    box-shadow: none;

    &:focus {
        box-shadow: none;
    }
}

.custom-select__results {
    padding-right: 22px;
    @include flexCenter;

    span {
        margin-right: 10px;

        &:nth-child(1) {
            flex-grow: 1;
        }

        &:nth-child(2) {
            margin-left: auto;
        }

        &:nth-child(3) {
            min-width: 42px;
            margin-right: 0;
            text-align: right;
        }
    }
}

.select2-container .select2-selection--single .select2-selection__rendered {
    padding: 0 20px 0 10px;
    font-family: $f;
    font-size: 16px;

    .custom-select__results {
        padding-right: 8px;

        span {

            &:not(:first-child) {
                font-family: $f-light-i;
                color: $gray--light;
            }

            &:nth-child(1) {
                margin-right: 10px;
                flex-grow: 0;
            }

            &:nth-child(2) {
                margin-right: 10px;
                // margin-left: 0;
            }

            &:nth-child(3) {
                min-width: 42px;
                margin-right: 0;
            }
        }

        //.select2-container .select2-selection--single .select2-selection__rendered .custom-select__results span
    }

    //.select2-container .select2-selection--single .select2-selection__rendered .custom-select__results
}

//.select2-container .select2-selection--single .select2-selection__rendered

.select2-container--default {

    .select2-results__option {

        &[aria-selected='true'] {
            color: $color-text;
            background-color: transparent;
            position: relative;

            &::after {
                content: '\f00c';
                @include fa('light');
                font-size: 14px;
                color: #bbb;
                @include centre('y');
                right: 5px;
            }

            //.select2-container--default .select2-results__option[aria-selected=true]::after
        }

        //.select2-container--default .select2-results__option[aria-selected='true']

        &--highlighted {

            &[aria-selected] {
                color: #000;
                background-color: $color-bg--light;
            }
        }

        //.select2-container--default .select2-results__option--highlighted
    }

    //.select2-container--default .select2-results__option

    .select2-selection--multiple,
    .select2-selection--single {
        background-color: transparent;
        border: 1px solid $color-border--light;
        transition: border 0.3s ease;

        &:hover {
            border-color: $color-border;
        }
    }

    //.select2-container--default .select2-selection--multiple, .select2-container--default .select2-selection--single

    .select2-selection--multiple {
        padding: 3px 0 1px;
        @include flexCenter;

        .select2-selection__choice {
            margin: 1px 5px 3px 0;
        }

        //.select2-container--default .select2-selection--multiple .select2-selection__choice
    }

    //.select2-container--default .select2-selection--multiple

    .select2-selection--single {
        height: 40px;
    }

    //.select2-container--default .select2-selection--single

    &.select2-container--open.select2-container--below {

        .select2-selection--multiple,
        .select2-selection--single {

            &:hover {
                border: 1px solid $color-border--lighten;
            }
        }
    }

    .select2-container--default .select2-selection--single .select2-selection--multiple {
        height: auto;
        line-height: 1;
        border-radius: $border-radius;

        .select2-selection__rendered {
            height: auto;
            max-height: 170px;
            line-height: 1;
            padding: 0 10px;
            @include bb-scroll;
        }

        //.select2-container--default .select2-selection--multiple .select2-selection__rendered

        .select2-selection__choice {
            height: auto;
            line-height: 21px;
            margin-top: 8px;
            padding: 0 20px 0 10px;
            font-size: 13px;
            color: #fff;
            border: none;
            border-radius: 50px;
            background-color: #5897fb;
            position: relative;

            &__remove {
                font-size: 18px;
                color: #fff;
                position: absolute;
                right: 3px;
                top: 0;
            }

            //.select2-container--default .select2-selection--multiple .select2-selection__choice__remove
        }

        //.select2-container--default .select2-selection--multiple .select2-selection__choice
    }

    //.select2-container--default .select2-selection--multiple
}

//.select2-container--default

.select2-container--default.select2-container--focus .select2-selection--multiple {
    border-color: $color-border--light;
}

.select2-selection__rendered {
    height: 100%;
}

.select2-container--default .select2-search--inline .select2-search__field {
    height: 32px;
    line-height: 30px;
    margin-top: 2px;
    font-size: 13px;
}

.select2-selection .select2-selection--multiple {
    height: 32px;
    line-height: 32px;
}

.select2-container--open .select2-dropdown--above {
    z-index: z(modal);
}

.select2-container--default.select2-container--disabled .select2-selection--single {
    background-color: $gray--lighten !important;
    border-color: $gray--lighten !important;

    b::before {
        color: $gray--light;
    }
}

.select2-container--default .select2-selection--multiple .select2-selection__rendered {
    padding: 0 10px;
}

.select2-container--default .select2-selection--single .select2-selection__clear {
    position: absolute;
    right: 6px;
    z-index: 10;

    i {
        font-size: 14px;
        background-color: #fff;
    }
}

//.select2-container--default .select2-selection--single .select2-selection__clear

.js-select--multiple {

    &.no-search {

        &+.select2-container--default {

            .select2-search--inline {

                .select2-search__field {
                    display: none;
                }
            }

            .select2-selection--multiple {
                min-height: 40px;
            }
        }
    }
}
