.hero {
    margin-bottom: $gutter;
    padding: 80px 0;
    color: #fff;
    text-align: center;
    position: relative;

    @include respond-to(sm) {
        margin-bottom: $gutter * 2;
    }

    &__inner {
        @include flexColumn;
    }

    &__title {
        display: block;
        margin-bottom: $gutter;
        font-family: $f-medium;
        font-weight: 500;
    } //.hero__title

    &__subtitle {
        font-family: $f;
        font-weight: 400;
        font-size: 23px;
    } //.hero__subtitle

    &__widget {
        max-width: 300px;
        padding: 10px 50px;
        color: $color-text;
        @extend %boxWidget;
        @include vx;
        bottom: -20px;

        @include respond-to(sm) {
            width: 100%;
            padding: 10px 15px;
            bottom: -28px;
        }

        .text {
            @include respond-to(sm) {
                display: none;
            }
        }
    } //.hero__widget

    &__social {
        @include respond-to(sm) {
            max-width: 300px;
            width: 100%;
            margin: 50px auto 0;
        }
    } //.hero__social

    &__button-box {
        margin-top: $gutter;

        .btn {
            min-width: 205px;
            margin: $gutter 0;
            font-family: $f-medium;
        }

        .bb-title--devider {
            max-width: 205px;
            margin: auto;
        }
    } //.hero__button-box

    &--icon {
        height: auto;
        margin-bottom: 0;
        background-repeat: no-repeat;
        background-position: center center;
        background-size: contain;

        @include respond-to(xs) {
            padding: 30px 0;
            margin-bottom: 0;
        }
    } //.hero__button-box
} //.hero

.hero-inco {

    &__inner {
        @include flexColumn;
        justify-content: center;
    }

    &__img {
        width: 250px;
        height: 250px;
        margin: 0 auto 50px;
        padding: 30px;
        border: 10px solid #000;
        border-radius: 50%;
        background-color: #ffa4eb;
        overflow: hidden;

        @include respond-to(sm) {
            width: 155px;
            height: 155px;
            margin-bottom: 20px;
            border: 5px solid #000;
        }
    }

    &__text {
        font-family: $f-bold-i;
        font-size: 40px;
        line-height: 1.4;
        text-shadow: 2px 2px 4px rgba(#000, 0.7);
        text-transform: uppercase;

        @include respond-to(sm) {
            font-size: 32px;
        }
    }
} //.hero-inco

.card-services-item {

    &:last-child {
        border-bottom: 0;
    }

    &:hover {
        @include respond-to(sm) {
            background-color: #fff;
        }
    }

    &.is-booked {
        @include respond-to(sm) {
            background-color: $color-bg--lighten;
        }
    }
} //.card-services-item
