.registration {

    .firstscreen {

        &__title {
            margin-bottom: 20px;

            @include respond-min(md) {
                margin-bottom: 30px;
            }
        } //firstscreen__title

        &__subtitle {
            margin-bottom: 50px;
        } //firstscreen__subtitle

        &__btn {
            position: absolute;
            bottom: 30px;
        } //firstscreen__btn

        .mouse-icon {
            align-self: center;

            @include respond-min(md) {
                position: absolute;
                bottom: -100px;
                left: 50%;
                transform: translateX(-50%);
            }
        }
    } //.firstscreen

    .screen {

        &__desc {
            margin-bottom: 40px;

            @include respond-min(md) {
                text-align   : center;
                font-size: 20px;
                margin-bottom: 60px;
            }

            p + p {
                margin-top: 10px;
            }
        } //.screen__desc

        &__icon-slider {

            @include respond-min(md) {
                width: 350px;
                margin: 0;
                order: 1;
                margin-right: auto;
            }

            .bb-slider__slide + .bb-slider__slide {

                @include respond-min(md) {
                    margin-top: 20px;
                }
            }
        } //.screen__icon-slider

        &--instr {

            .text-block__wrap {
                margin-bottom: 60px;

                @include respond-min(md) {
                    margin-bottom: 30px;
                }

                .text-block:nth-child(5)
                .text-block__icon {
                    margin-left: 73px;
                }
            } //.text-block__wrap
        } //.screen__instr

        &--get-icons {

            .container {
                @include respond-min(md) {
                    max-width: 900px;
                }
            }

            .screen__inner {
                margin-bottom: 20px;
                @include respond-min(md) {
                    display: flex;
                    align-items: center;
                    margin-bottom: 60px;
                }
            }

            .list--square {
                margin-bottom: 50px;
                @include respond-min(md) {
                    margin-right: 60px;
                    margin-bottom: 0;
                }
            }
        } //.screen--get-icons

        &--about {
            padding-bottom: 0;
            @include respond-min(md) {
                padding-bottom: 60px;
            }

            .screen__title {
                margin-bottom: 28px;
            }
        } //.screen--about

        &--adv {

            .text-block {
                margin-top: 10px;
                @include respond-min(md) {
                    margin-top: 0;
                }

                & + .text-block {
                    margin-top: 30px;
                    @include respond-min(md) {
                        margin-top: 0;
                    }
                }
            }

            img {
                @include respond-min(md) {
                    width: 80%;
                    margin: auto;
                }
            }

            .screen__img {
                @include respond-min(md) {
                    display: inline-block;
                    width   : 100%;
                    margin-bottom: 60px;
                }

                img {
                    width: 80%;
                    margin: auto;
                }
            }
        } //.screen--advantages

        &--join-us {
            background-image: url('../img/general/onepage/registration/manicure.jpg');
            @include sectionOverlay;
            @include cover;
        } //.screen--join-us

        &--reg {
            padding: 20px 0;

            @include respond-min(md) {
                display: none;
            }
        } //.screen--reg

        &--reviews {

            .bb-slider__slide {

                @include respond-min(md) {
                    padding: 0 30px;
                }
            }
        }
    } //.screen

    .bb-list {
        margin-bottom: 30px;
    } //.bb-list

    .icon-list {

        &__wrap {
            margin-bottom: 50px;

            @include respond-min(md) {
                margin-bottom: 70px;
            }
        } //icon-list__wrap
    } //.icon-list

    .video__wrapper {
        @include respond-min(md) {
            width: 1000px;
            margin: auto;
        }
    }

    .container {
        padding: 0 20px;
    }
} //.registration



