.datepicker {
    font-family: $f;
    font-size: 13px;
    border: 1px solid $color-border--lighten;
    border-radius: $border-radius;
    box-shadow: 0 5px 20px rgba($gray, 0.25);
    z-index: z(dropdown);

    @include respond-to(xs) {
        z-index: z(modal);
    }

    &--pointer {
        width: 12px;
        height: 12px;
        border-top: 1px solid $color-border--lighten;
        border-right: 1px solid $color-border--lighten;
        border-top-right-radius: $border-radius;
        bottom: calc(100% - 6px) !important;
    } //.datepicker--pointer

    &--cell {
        font-family: $f-medium;
        font-size: 13px;

        &-day {
            border: 1px solid #fff;
        } //.datepicker--cell-day

        &.-current- {
            color: $gray--darkest;

            &:hover {
                border-color: transparent;
            }

            &.-in-range- {
                @extend .datepicker--cell.-current-;
            }
        } //..datepicker--cell.-current-

        &.-focus- {
            background-color: rgba($color-main, 0.1);
        } //.datepicker--cell.-focus-

        &.-selected- {
            border: 1px solid #fff;
            background-color: $color-main;

            &.-focus- {
                background-color: $color-main;
            }

            &.-current- {
                @extend .datepicker--cell.-selected-;
            }
        } //.datepicker--cell.-selected-

        &.is-checked {
            background-color: $gray--lighten;

            &:hover {
                background-color: darken($gray--lighten, 5%);
            }

            &.-other-month- {
                color: #fff;
            }

            &.-selected- {
                background-color: $color-main;
            }
        } //.datepicker--cell.is-checked
    } //.datepicker--cell

    &--inline {

        .datepicker {

            &--days-names {
                margin: 0;
                padding: 8px 0;
            }
        }
    } //.datepicker--inline
} //.datepicker

.-selected-.datepicker--cell-day.-other-month- {
    background: $color-main;
}

.-selected-.-focus-.datepicker--cell-day.-other-month- {
    background: $color-main;
}

.datepicker-inline {
    background-color: $color-bg--lightest;
    @include flexColumn;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: z(fixed);
    @include visibleBlock;

    &.is-open {
        z-index: z(dropdown) + 222;
    }

    &__inner,
    &__header,
    &__btn {
        background-color: #fff;
    }

    &__inner {
        flex-grow: 1;
        border-bottom: 1px solid $color-border--lightest;
    } // .datepicker-inline__inner

    &__header {
        height: 50px;
        padding: 0 10px;
        font-family: $f-light;
        font-size: 18px;
        line-height: 1;
        color: $gray--darkest;
        text-align: center;
        border-bottom: 1px solid $color-border--lightest;
        position: relative;
        @include flexCenter;
        justify-content: center;
    } // .datepicker-inline__header

    &__btn {
        padding: $gutter $gutter / 2;
        text-align: center;
        border-top: 1px solid $color-border--lightest;
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
    } // .datepicker-inline__btn

    &__close {
        @include vy;
        right: 10px;
    } // .datepicker-inline__close

    .datepicker {
        max-width: 320px;
        margin: 0 auto;
        border: 0;

        &--nav {
            border: 0;
        }
    } // .datepicker-inline .datepicker
} // .datepicker-inline
