.row {
    @include row-flex;
}

@for $i from 1 through $columns {

    .col-#{$i} {
        @include col;
        @include size($i);
    }

    @include respond-to(lg) {

        .col-lg-#{$i} {
            @include size-lg($i);
        }
    }

    @include respond-to(md) {

        .col-md-#{$i} {
            @include size-md($i);
        }
    }

    @include respond-to(sm) {

        .col-sm-#{$i} {
            @include size-sm($i);
        }
    }

    @include respond-to(xs) {

        .col-xs-#{$i} {
            @include size-xs($i);
        }
    }
}
